import {HttpClient, HttpHeaders} from "@angular/common/http";
import {Injectable} from "@angular/core";
import {RecruiterProfiles} from "src/models/recruiter.service.model";
import {END_POINT} from "../models/constants";

@Injectable({
  providedIn: "root",
})
export class RecruiterService {
  constructor(private http: HttpClient) {
  }

  public async get_recruiters_profiles(body: RecruiterProfiles): Promise<String> {

    const httpHeaders: HttpHeaders = new HttpHeaders();
    return await this.http.get<String>(sample_request, { headers: httpHeaders}).toPromise();
  }
}

const sample_request = 'https://reqres.in/api/users/2';

const URI: string =
  `${END_POINT}/recruiterprofiles/`;
