import { SelectionModel } from '@angular/cdk/collections';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { SpinnerVisibilityService } from 'ng-http-loader';
import { ApiService } from 'src/app/api.service';
import { AlertDialogComponent } from 'src/app/util/alert-dialog/alert-dialog.component';

export interface UserData {
  position: number;
  name: string;
  skillid: number;
  hide: boolean;
  count: number;
}
const ELEMENT_DATA: UserData[] = [
  // { position: 0, name: 'Angular', hide: false,count: 0 }
];

@Component({
  selector: 'app-view-questions',
  templateUrl: './view-questions.component.html',
  styleUrls: ['./view-questions.component.css']
})

export class ViewQuestionsComponent implements OnInit {

  question_image_path = ""
  all_question_keys = []
  selected_question_key = ""
  pagesize = 50
  pageno = 1
  totalquestions = 1000
  low_complexity = []
  medium_complexity = []
  high_complexity = []
  filter_skills = []
  filter_complexity = []
  filter_complexity_all = false
  filter_complexity_low = false
  filter_complexity_medium = false
  filter_complexity_high = false
  question_skills = {}
  skills_array = []
  dataSource = new MatTableDataSource<UserData>(ELEMENT_DATA);
  displayedColumns: string[] = ['select', 'name'];
  selection = new SelectionModel<UserData>(true, []);
  selected_skills: UserData[] = [
  ];

  dataSourceMapped = new MatTableDataSource<UserData>(ELEMENT_DATA);
  displayedColumnsMapped: string[] = ['select', 'name'];
  selectionMapped = new SelectionModel<UserData>(true, []);
  selected_skillsMapped: UserData[] = [
  ];

  delete_question_no: number
  questions = []
  question_ids = []
  question_edit = []
  question_keys = []
  constructor(private apiService: ApiService, private spinnerVisibilityService: SpinnerVisibilityService, private matDialog: MatDialog) { }

  ngOnInit(): void {
    this.pageno = 1
    this.fetchquestions({ question_key: this.selected_question_key,complexity: this.filter_complexity, skills: this.filter_skills, start: (this.pageno - 1) * this.pagesize, end: this.pagesize })
    this.fetch_skills("")
    this.fetchquestionkeys()
  }
  editquestion(i, check) {
    check ? this.question_edit[i] = true : this.question_edit[i] = false
    // this.fetch_question_skills(i)
  }
  savequestion(i) {
    let skills_array = []
    this.selected_skillsMapped.forEach(element => {
      element.hide ? skills_array.push(element.skillid) : ""
    });
    this.question_edit[i] = false
    let input = {
      id: this.question_ids[i],
      question: this.questions[i],
      action: "update",
      skill_mapping: skills_array
    }
    console.log(input)
    console.log(this.questions[i])
    this.spinnerVisibilityService.show();
    this.apiService.updatequestions(input).then((data) => {
      console.log(data)
      this.openDialog("Updated successfully", "success")
    }).finally(() => {
      this.fetchquestions({ question_key: this.selected_question_key,complexity: this.filter_complexity, skills: this.filter_skills, start: (this.pageno - 1) * this.pagesize, end: this.pagesize })
      this.spinnerVisibilityService.hide();
    });
  }

  openDialog(message: string, type: string) {
    const dialogConfig = new MatDialogConfig();
    // dialogConfig.data = message;
    dialogConfig.data = { "message": message, "type": type };
    let dialogRef = this.matDialog.open(AlertDialogComponent, dialogConfig);
  }

  fetchquestions(skills: any) {
    this.questions = []
    this.question_ids = []
    this.question_edit = []
    this.low_complexity = []
    this.medium_complexity = []
    this.high_complexity = []
    this.question_keys = []
    this.spinnerVisibilityService.show();
    this.apiService.fetchallquestions(skills).then((data) => {
      // console.log(data)
      this.totalquestions = data['count']
      this.question_skills = data['skills']
      data['questions'].forEach((element, index) => {
        this.questions.push(JSON.parse(element['question']))
        this.question_ids.push(JSON.parse(element['question_id']))
        this.question_keys.push(element['question_key'])
        this.question_edit.push(false)
        if (element['complexity'] == "L") {
          this.low_complexity[index] = true
          this.medium_complexity[index] = false
          this.high_complexity[index] = false
        }
        if (element['complexity'] == "M") {
          this.low_complexity[index] = false
          this.medium_complexity[index] = true
          this.high_complexity[index] = false
        }
        if (element['complexity'] == "H") {
          this.low_complexity[index] = false
          this.medium_complexity[index] = false
          this.high_complexity[index] = true
        }
      });
    }).finally(() => {
      this.spinnerVisibilityService.hide();
    });;

  }

  deletequestion() {
    let input = {
      id: this.question_ids[this.delete_question_no],
      question: { "a": "b" },
      action: "delete"
    }
    this.spinnerVisibilityService.show();
    this.apiService.updatequestions(input).then((data) => {
      console.log(data)
      this.openDialog("Deleted successfully", "success")
    }).finally(() => {
      this.fetchquestions({ question_key: this.selected_question_key,complexity: this.filter_complexity, skills: this.filter_skills, start: (this.pageno - 1) * this.pagesize, end: this.pagesize })
      this.spinnerVisibilityService.hide();
    });
    console.log(this.delete_question_no)
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  showSkills(row) {
    this.pageno = 1
    this.dataSource.data[row.position].hide = !this.dataSource.data[row.position].hide;
    this.selected_skills = this.dataSource.data
    this.filter_skills = []
    this.selected_skills.forEach(element => {
      element.hide ? this.filter_skills.push(element.skillid) : ""
    });
    this.fetchquestions({ question_key: this.selected_question_key,complexity: this.filter_complexity, skills: this.filter_skills, start: (this.pageno - 1) * this.pagesize, end: this.pagesize })
  }

  applyFilterMapped(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSourceMapped.filter = filterValue.trim().toLowerCase();
  }

  showSkillsMapped(row) {
    this.dataSourceMapped.data[row.position].hide = !this.dataSourceMapped.data[row.position].hide;
    this.selected_skillsMapped = this.dataSourceMapped.data

  }

  fetch_skills(filterskill: any) {
    this.apiService.fetchSkills(filterskill).then((data) => {
      let skillarray: UserData[] = []
      let skillarrayMapped: UserData[] = []
      data.forEach((element, index) => {
        skillarray.push({ position: index, skillid: element.id, name: element.name, hide: false, count: 0 })
        skillarrayMapped.push({ position: index, skillid: element.id, name: element.name, hide: false, count: 0 })
      });
      let temp_dataSource = new MatTableDataSource<UserData>(skillarray);
      let temp_dataSourceMapped = new MatTableDataSource<UserData>(skillarrayMapped);
      this.dataSource = temp_dataSource
      this.dataSourceMapped = temp_dataSourceMapped
    });
  }

  view_question(i) {
    console.log(i)
    this.fetch_question_skills(i)
  }

  fetch_question_skills(i) {
    let input = {
      question_id: this.question_ids[i]
    }
    this.spinnerVisibilityService.show();
    this.dataSourceMapped.data.forEach((element_datasource, index) => {
      this.dataSourceMapped.data[index].hide = false
    })
    this.apiService.fetchQuestionSkills(input).then((data) => {
      console.log(data)
      data.forEach((element, index) => {
        // this.question_skills.push(element.name)
        this.dataSourceMapped.data.forEach((element_datasource, index) => {
          // this.dataSourceMapped.data[index].hide = false
          if (element_datasource.skillid == element.id) {
            console.log("datasource", element_datasource.skillid)
            console.log("element", element.id)
            this.dataSourceMapped.data[index].hide = true
            console.log(this.dataSourceMapped.data[index])
          }
        });
      });
    }).finally(() => {
      this.spinnerVisibilityService.hide();
    });;
  }

  complexity_bulk_update() {
    console.log(this.low_complexity)
    console.log(this.medium_complexity)
    console.log(this.high_complexity)
    let complexity_array = []
    let input = {}
    let complexities = []

    for (let x = 0; x <= Math.max(this.low_complexity.length, this.medium_complexity.length, this.high_complexity.length); x++) {
      complexity_array[x] = ""
      this.low_complexity[x] ? complexity_array[x] = "L" : ""
      this.medium_complexity[x] ? complexity_array[x] = "M" : ""
      this.high_complexity[x] ? complexity_array[x] = "H" : ""
    }
    console.log(complexity_array)
    this.question_ids.forEach((element, index) => {
      complexities.push({
        "question_id": element,
        "complexity": index < complexity_array.length ? complexity_array[index] : ""
      })
    });
    input = {
      "complexities": complexities
    }
    this.apiService.complexity_bulk_update(input).then((data) => {
      console.log(data)
    })
  }
  prevpage() {
    this.pageno > 1 ? this.pageno = this.pageno - 1 : ""
    this.fetchquestions({ question_key: this.selected_question_key,complexity: this.filter_complexity, skills: this.filter_skills, start: (this.pageno - 1) * this.pagesize, end: this.pagesize })
  }

  nextpage() {
    this.pageno * this.pagesize < this.totalquestions ? this.pageno = this.pageno + 1 : ""
    this.fetchquestions({ question_key: this.selected_question_key,complexity: this.filter_complexity, skills: this.filter_skills, start: (this.pageno - 1) * this.pagesize, end: this.pagesize })
  }
  complexity_change(complexity, type, i) {
    if (complexity == true) {
      if (type == "L") {
        this.medium_complexity[i] = false
        this.high_complexity[i] = false
      }
      if (type == "M") {
        this.low_complexity[i] = false
        this.high_complexity[i] = false
      }
      if (type == "H") {
        this.low_complexity[i] = false
        this.medium_complexity[i] = false
      }
    }
  }
  filter_complexity_populate() {
    this.filter_complexity = []
    this.filter_complexity_all ? this.filter_complexity = ['L', 'M', 'H'] : ""
    this.filter_complexity_low ? this.filter_complexity.push('L') : ""
    this.filter_complexity_medium ? this.filter_complexity.push('M') : ""
    this.filter_complexity_high ? this.filter_complexity.push('H') : ""
    console.log(this.filter_complexity)
    this.fetchquestions({ question_key: this.selected_question_key,complexity: this.filter_complexity, skills: this.filter_skills, start: (this.pageno - 1) * this.pagesize, end: this.pagesize })
  }

  fetchquestionkeys() {
    let input = ""
    this.apiService.fetchallquestionkeys(input).then((data) => {
      this.all_question_keys = []
      data.forEach(element => {
        this.all_question_keys.push(element['question_key'])
      });
      console.log(this.all_question_keys)
      
    })
  }

  filter_question_key(){
    this.pageno = 1
    console.log(this.selected_question_key)
    this.fetchquestions({ question_key: this.selected_question_key,complexity: this.filter_complexity, skills: this.filter_skills, start: (this.pageno - 1) * this.pagesize, end: this.pagesize })
  }

  split_text(text:string){
    console.log(text.split(":")[1])
    if (text.split(":")[0] == "IMAGE"){
      this.question_image_path = "assets/assessment/questions/images/psychometric/" + text.split(":")[1] + ".jpg"
      console.log(this.question_image_path)
      return text.split(":")[1]
    }else{
      this.question_image_path=""
      return ""
    }
  }
}
