import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {END_POINT, HTTP_HEADERS} from "../../../models/constants";
import {firstValueFrom} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class ResumeService {

  constructor(private http: HttpClient) {
  }


  public async uploadResume(file: File) {
    const URL = `${END_POINT}/v1/candidate/resume`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    const putURL = await firstValueFrom(this.http.put<string>(URL, {}, {headers: httpHeaders}));
    await firstValueFrom(this.http.put(putURL, file))
  }


  public async getResume(): Promise<string> {
    const URL = `${END_POINT}/v1/candidate/resume`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    return await firstValueFrom(this.http.get<string>(URL, {headers: httpHeaders}));
  }
}
