import {Component, Input, OnInit} from '@angular/core';
import {Observable} from "rxjs";
import {cities} from "../../../assets/Database/worldcities";
import {map, startWith} from "rxjs/operators";
import {AbstractControl} from "@angular/forms";

@Component({
  selector: 'app-location-select',
  templateUrl: './location-select.component.html',
  styleUrls: ['./location-select.component.css']
})
export class LocationSelectComponent implements OnInit {

  filteredCities: Observable<string[]>;
  @Input('locationFormControl') location: AbstractControl;

  constructor() {
    //
  }

  ngOnInit(): void {

    this.filteredCities = this.location.valueChanges.pipe(
      startWith(''),
      map(value => this._filter(value || '')),
    )

  }

  private _filter(value: string): string[] {
    const filterValue = this._normalizedValue(value);

    if (value.length < 3) {
      return [];
    }

    return cities.filter(city => this._normalizedValue(city).includes(filterValue));
  }

  private _normalizedValue(value: string): string {
    return value.toLowerCase().replace(/\s/g, '');
  }

}
