import {Injectable} from '@angular/core';
import {Observable, Subject} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class HeaderUpdateService {

  private subjectName = new Subject<HeaderUpdateEvent>(); //need to create a subject

  constructor() {
  }


  sendUpdate(message: HeaderUpdateEvent) { //the component that wants to update something, calls this fn
    this.subjectName.next(message); //next() will feed the value in Subject
  }

  getUpdate(): Observable<HeaderUpdateEvent> { //the receiver component calls this function
    return this.subjectName.asObservable(); //it returns as an observable to which the receiver function will subscribe
  }

}

export enum HeaderUpdateEvent {
  SIGN_IN = "SIGN_IN",
  SIGN_OUT = "SIGN_OUT",
  NOT_FOUND = "NOT_FOUND",
  FOUND = "FOUND",
  IMAGE_CHANGE = "IMAGE_CHANGE",
  START_TEST = "START_TEST",
  END_TEST = "END_TEST",
}

