import {Component, ElementRef, EventEmitter, OnInit, Output, ViewChild} from '@angular/core';
import {SkillsService} from "../../../../services/candidate/my-profile/skills.service";
import {FormBuilder, FormGroup} from "@angular/forms";
import {Skills, SkillsTagLine, TaglineAndSkills} from "../../../../models/candidate.model";
import {PUNCHLINE, SKILLS, SKILLS_CTRL} from "../../../../models/constants";
import {MatAutocompleteSelectedEvent} from "@angular/material/autocomplete";
import {MatChipInputEvent} from "@angular/material/chips";
import {COMMA, ENTER} from "@angular/cdk/keycodes";
import {map, startWith} from "rxjs/operators";
import {Observable} from "rxjs";
import {AlertDialogComponent} from "../../../util/alert-dialog/alert-dialog.component";
import {MatDialog} from "@angular/material/dialog";
import {HAS_ROLE} from "../../../../models/auth.constants";
import {Roles} from "../../../../models/auth.model";

@Component({
  selector: 'candidate-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.css']
})
export class SkillsComponent implements OnInit {


  punchLines: SkillsTagLine[] = [];
  filteredSkills: Observable<any>;
  separatorKeysCodes: number[] = [ENTER, COMMA]
  @ViewChild('skillsInput') skillInput: ElementRef<HTMLInputElement>;
  @Output('taglineUpdate') tagLineUpdateEmitter: EventEmitter<string> = new EventEmitter<string>();
  skillForm: FormGroup = this.formBuilder.group({
    punchLine: '',
    skills: [],
    skillCtrl: '',
  });
  isInterviewer: boolean;

  constructor(private skillsService: SkillsService,
              private formBuilder: FormBuilder,
              private matDialog: MatDialog) {
    this.filteredSkills = this.skillForm.get(SKILLS_CTRL).valueChanges
      .pipe(
        startWith(null),
        map((skill: string) => (skill ? this._filterSkills(skill) : this.allSkills.slice())),
      );

  }

  private _allSkills: Skills[] = [];

  get allSkills(): Skills[] {
    return this._allSkills.filter(skill => !this.mandatorySkills.find(_skill => {
      return _skill.id === skill.id;
    }));
  }

  set allSkills(value: Skills[]) {
    this._allSkills = value;
  }

  get userSkills(): Array<Skills> {
    return this.skillForm.get(SKILLS).value || [];
  }

  set addUserSkills(skill: Skills) {
    const isSkillAlreadyAvailable = this.userSkills
      .map(_skill => _skill.id)
      .filter(id => id === skill.id).length !== 0;
    if (!isSkillAlreadyAvailable) {
      if (!this.isInterviewer || (this.userSkills?.length < 5 && this.isInterviewer)) {
        this.userSkills.push(skill)
      }
    }
  }

  get userPunchline(): number {
    return this.skillForm.get(PUNCHLINE).value;
  }

  get mandatorySkills(): Skills[] {
    return this.punchLines
      .filter(punchLine => this.userPunchline == punchLine.id)
      .map(punchline => punchline.mandatorySkills)
      .filter(skills => skills !== null)
      .reduce((prev, curr) => [...prev, ...curr], []);
  }

  async ngOnInit(): Promise<void> {
    return this.init()
  }

  async updateSkills() {
    await this.skillsService.patchUserSkills({
      tagline: this.userPunchline,
      skills: this.userSkills.map(skills => skills.id)
    });
    this.matDialog.open(AlertDialogComponent, {data: {message: 'Skills Updated!'}});
    this.tagLineUpdateEmitter.emit(this.punchLines.filter(data => data.id === this.userPunchline)[0]?.name)
  }

  addSkill(event: MatChipInputEvent): void {
    const value = event?.value?.trim();
    if (value) {
      // this.userSkills.push(value);
    }
    event.chipInput.clear();
    this.skillForm.get(SKILLS_CTRL).setValue(null)
  }

  removeSkill(skill: Skills) {
    this.skillForm.get(SKILLS).setValue(this.userSkills.filter(_skill => _skill.skill_name !== skill.skill_name));
  }

  selectedSkills(event: MatAutocompleteSelectedEvent): void {
    this.addUserSkills = this.allSkills.filter(_skill => _skill.skill_name === event.option.viewValue)[0]
    this.skillInput.nativeElement.value = '';
    this.skillForm.get(SKILLS_CTRL).setValue(null);
  }

  async init(): Promise<void> {
    this.isInterviewer = await HAS_ROLE(Roles.INTERVIEWER);
    Promise.all([
      this.skillsService.getAllTagLines()
        .then(data => this.punchLines = data),
      this.skillsService.getAllSkills()
        .then(data => this.allSkills = data),
    ])
      .then(_ => this.populateSkills().catch(err => console.error(err)))
  }

  private async populateSkills() {
    const taglineAndSkills: TaglineAndSkills = await this.skillsService.getUserSkillsAndTagline();
    this.skillForm.get(PUNCHLINE).setValue(taglineAndSkills.tagline?.id || '')
    this.skillForm.get(SKILLS).setValue(taglineAndSkills.skills)
  }

  private _filterSkills(skill: string): Skills[] {
    return this.allSkills
      .filter(_skill => _skill.skill_name.toLowerCase().includes(skill?.toString().toLowerCase()))
  }

}
