import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {END_POINT, HTTP_HEADERS} from "../../../models/constants";
import {firstValueFrom} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class ProfileImageService {

  constructor(private http: HttpClient) {
  }

  public async uploadProfileImage(file: File): Promise<void> {
    const URL = `${END_POINT}/v1/candidate/profile-image`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    const putURL = await firstValueFrom(this.http.put<string>(URL, {}, {headers: httpHeaders}));
    await firstValueFrom(this.http.put(putURL, file));
  }

  public async getProfileImage(): Promise<string> {
    const URL = `${END_POINT}/v1/candidate/profile-image`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    return firstValueFrom(this.http.get<string>(URL, {headers: httpHeaders}));
  }
}
