import {AbstractControl, FormGroup, ValidationErrors, ValidatorFn} from "@angular/forms";

export class PasswordValidators {
  static readonly PASSWORD = 'password';
  static readonly OLD_PASSWORD = 'oldPassword';
  static readonly NEW_PASSWORD = 'newPassword';
  static readonly CONFIRM_PASSWORD = 'confirmPassword';

  static checkConfirmPassword(group: AbstractControl): ValidationErrors | null {
    const pass = group.get(PasswordValidators.PASSWORD).value;
    const confirmPass = group.get(PasswordValidators.CONFIRM_PASSWORD).value
    return pass === confirmPass
      ? null : {confirmPassword: false}
  }

  static checkValidPassword(group: AbstractControl): ValidationErrors | null {
    const pass: string = group.value;
    const passwordRegex = /(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}/g;
    return pass.match(passwordRegex)
      ? null : {confirmPassword: false}
  }

  static readonly validateConfirmPassword: ValidatorFn = (formGroup: FormGroup) => {
    const newPassword = formGroup.get(PasswordValidators.NEW_PASSWORD);
    const confirmPassword = formGroup.get(PasswordValidators.CONFIRM_PASSWORD);
    return confirmPassword.dirty && confirmPassword.value !== newPassword.value ? {message: "password do not match"} : null;
  }
}
