import { Component, OnInit } from '@angular/core';
import { AssessmentEnum, AssessmentView } from "../../../models/candidate.model";

@Component({
  selector: 'candidate-assessment',
  templateUrl: './assessment.component.html',
  styleUrls: ['./assessment.component.css']
})
export class AssessmentComponent implements OnInit {

  assessmentView: AssessmentView = { test: true };

  constructor() {
    //
  }

  ngOnInit(): void {
    //
  }

  get AssessmentEnum(): typeof AssessmentEnum{
    return AssessmentEnum;
  }

  public updateView(view: AssessmentEnum) {
    switch (view) {
      case AssessmentEnum.TEST:
        this.assessmentView = { test: true };
        break;
      case AssessmentEnum.SUMMARY:
        this.assessmentView = { summary: true };
        break;
      case AssessmentEnum.PROFILE_ACTIVITY:
        this.assessmentView = { profileActivity: true };
        break;
      case AssessmentEnum.INTERVIEW:
        this.assessmentView = { interview: true };
        break;
      case AssessmentEnum.INTERVIEWER:
        this.assessmentView = { interviewer: true };
        console.log(this.assessmentView.interviewer)
        break;
    }
  }

}
