import {Component, OnDestroy, OnInit} from '@angular/core';
import {FormBuilder, FormControl, Validators} from "@angular/forms";
import {PasswordValidators} from "../../../util/validators.util";
import {Auth} from "aws-amplify";
import {MatDialog} from "@angular/material/dialog";
import {AlertDialogComponent} from "../../util/alert-dialog/alert-dialog.component";
import {Subscription} from "rxjs";
import {AuthService} from "../../../services/auth/auth.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit, OnDestroy {

  private static readonly USER_NAME = 'userName';
  private static readonly OTP = 'otp';
  resetPasswordFormGroup = this.formBuilder.group({
    userName: ['', {validators: [Validators.required]}],
    otp: ['', {validators: [Validators.required], updateOn: 'change'}],
    newPassword: ['', {validators: [Validators.required]}],
    confirmPassword: ['', {validators: [Validators.required]}],
  }, {validators: [PasswordValidators.validateConfirmPassword], updateOn: "blur"});
  otpRequestSend = false;
  private _usernameValueChange: Subscription;

  constructor(private formBuilder: FormBuilder,
              private authService: AuthService,
              private matDialog: MatDialog,
              private router: Router) {
  }

  private _errorMessage: string;

  get errorMessage() {
    return this.resetPasswordFormGroup.errors?.['message'] || this._errorMessage;
  }

  get otpFormControl(): FormControl {
    return this.resetPasswordFormGroup.get(ResetPasswordComponent.OTP) as FormControl;
  }

  get newPasswordFormControl(): FormControl {
    return this.resetPasswordFormGroup.get(PasswordValidators.NEW_PASSWORD) as FormControl;
  }

  get confirmPasswordFormControl(): FormControl {
    return this.resetPasswordFormGroup.get(PasswordValidators.CONFIRM_PASSWORD) as FormControl;
  }

  get userNameFormControl(): FormControl {
    return this.resetPasswordFormGroup.get(ResetPasswordComponent.USER_NAME) as FormControl;
  }

  async onClickGetOtp() {
    try {
      await Auth.forgotPassword(this.userNameFormControl.value);
      this.otpRequestSend = true;
      this.matDialog.open(AlertDialogComponent, {data: {message: 'OTP sent to your registered email address'}});
      this.otpFormControl.enable();
    } catch (e) {
      switch (e.code) {
        case 'UserNotFoundException':
          this._errorMessage = 'Invalid user name';
          break;
        case 'InvalidParameterException':
          const resp: boolean = await this.authService.resetPassword(this.userNameFormControl.value);
          if (resp) {
            this.matDialog.open(AlertDialogComponent, {
              data: {
                message: 'we have sent your password to your registered email, please use that password to login.',
                onClose: () => this.router.navigate(['/login']),
                closeButtonName: 'Login',
              }
            });
          } else {
            this._errorMessage = e.message;
          }
          break;
        case 'LimitExceededException':
          this._errorMessage = e.message;
          break;
        default:
          console.error(e);
          break;
      }
    }
  }

  async onClickResetPassword() {
    try {
      await Auth.forgotPasswordSubmit(this.userNameFormControl.value, this.otpFormControl.value, this.newPasswordFormControl.value);
      this.matDialog.open(AlertDialogComponent, {data: {
        message: 'Password Reset Successfully, You can login using your new password',
          onClose: () => this.router.navigate(['/login']),
          closeButtonName: 'Login',
      }});
    } catch (e) {
      switch (e.code) {
        case 'UserNotFoundException':
          this._errorMessage = 'Invalid User';
          break;
        case 'CodeMismatchException':
        case 'ExpiredCodeException':
        case 'LimitExceededException':
          this._errorMessage = e.message;
          break;
        default:
          console.error(e);
          break;
      }
    }
  }

  ngOnInit(): void {
    this.otpFormControl.disable();
    this._usernameValueChange = this.resetPasswordFormGroup.get(ResetPasswordComponent.USER_NAME)
      .valueChanges
      .subscribe(_ => this.otpRequestSend = false);
  }

  ngOnDestroy(): void {
    this._usernameValueChange?.unsubscribe()
  }

}
