import {HttpHeaders} from "@angular/common/http";
import {AUTH_GET_JWT_TOKEN} from "./auth.constants";
import {environment} from "../environments/environment";

export const FULL_NAME = 'fullName';
export const LOCATION = 'location';
export const MOBILE_NUMBER = 'mobileNumber';
export const EMAIL = 'email';
export const EXPERIENCE = 'experience';
export const SALARY = 'salary';
export const OPEN_FOR_CHANGE = 'openForChange';
export const PUNCHLINE = 'punchLine'
export const SKILLS = 'skills'
export const SKILLS_CTRL = 'skillCtrl';


export const AUTHORIZATION = 'Authorization';

export const END_POINT = environment.aws.mobile.API.endpoints
  .filter(data => data.name === 'serverless')
  .map(data => data.endpoint)[0];

export const HTTP_HEADERS = () => {
  return AUTH_GET_JWT_TOKEN()
    .then(data => `Bearer ${data}`)
    .then(data => new HttpHeaders({
      AUTHORIZATION: data
    }))
    .catch(err => {
      console.error(err);
      return new HttpHeaders();
    });
}


