import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { FormBuilder } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import {MatIconRegistry} from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';
import {ApiService} from "../../api.service";

const googleLogoURL =
"https://raw.githubusercontent.com/fireflysemantics/logo/master/Google.svg";

@Component({
  selector: 'app-candidate-login',
  templateUrl: './candidate-login.component.html',
  styleUrls: ['./candidate-login.component.css']
})
export class CandidateLoginComponent implements OnInit {

  constructor(private formBuilder: FormBuilder,private apiService:ApiService,private router: Router, private route: ActivatedRoute,private matIconRegistry: MatIconRegistry,
    private domSanitizer: DomSanitizer) {
      this.matIconRegistry.addSvgIcon(
        "logo",
        this.domSanitizer.bypassSecurityTrustResourceUrl(googleLogoURL));
    }

  ngOnInit(): void {
  }

  checkoutForm = this.formBuilder.group({
    name: '',
    email: '',
    password: '',
    mobno: '8800544771',
    exp_years: 10,
    exp_months: 5,
    resume: new FormControl('', [Validators.required]),
    fileSource: new FormControl('', [Validators.required]),
    terms_conditions: new FormControl('', [Validators.required])
  });

  private file: File;

  title = 'training-app';
  test: String;

 onFileChange(event) {

  if (event.target.files.length > 0) {
    const file = event.target.files[0];
    this.checkoutForm.patchValue({
      fileSource: file
    });
  }
}


 onClickSubmit(): void {
  this.router.navigate(['candidate/profile']);
   console.log(this.checkoutForm.get('terms_conditions').value);
  // Process checkout data here
  const formData = new FormData();
  formData.append('file', this.checkoutForm.get('fileSource').value);

  this.apiService.postFile(formData).subscribe((data)=>{
    console.log(data);
  });


  console.warn('Your order has been submitted', this.checkoutForm.value.resume);
  this.apiService.getNews().subscribe((data)=>{
    this.test = JSON.stringify(data)
    this.checkoutForm.patchValue({name: this.test,email: ''});
  });

  this.apiService.postNews(JSON.stringify(this.checkoutForm.value)).subscribe((data)=>{
    console.log(data);
  });

  console.log(JSON.stringify(this.checkoutForm.value))
}

}
