import { Component, ElementRef, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { MatIconRegistry } from '@angular/material/icon';
import { MatSidenav } from '@angular/material/sidenav';
import { DomSanitizer } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { ApiService } from './api.service';
import { onAuthUIStateChange, CognitoUserInterface, AuthState } from '@aws-amplify/ui-components';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {


  title = 'training-app';
  user: CognitoUserInterface | undefined;
  authState!: AuthState;

  @ViewChild('search') searchTextBox: ElementRef;

  teams: any[] = [
    { name: 'Angular front end developer' },
    { name: 'Java full stack with Angular' },
    { name: 'Java back end with spriselectedValuesng boot' },
    { name: 'DevOps' },
    { name: 'Selenium QE' },
    { name: 'Test Architect' }
  ];

  selectFormControl = new FormControl();
  searchTextboxControl = new FormControl();
  selectedValues = [];
  data: string[] = [
    'Core Java',
    'Angular 4+',
    'Spring boot',
    'Dev Ops',
    'Kubernetes',
    'AWS',
    'Jenkins',
    'GitLabs',
    'Python',
    'GO',
    'Terraform',
    'CloudFormation'
  ]

  filteredOptions: Observable<any[]>;

  @ViewChild('sidenav') sidenav: MatSidenav;
  isExpanded = true;
  showSubmenu: boolean = false;
  isShowing = false;
  showSubSubMenu: boolean = false;

  viewresume: boolean = false;
  updateresume: boolean = false;
  showskills: boolean = false;
  personaldetails: boolean = false;

  mouseenter() {
    if (!this.isExpanded) {
      this.isShowing = true;
    }
  }

  mouseleave() {
    if (!this.isExpanded) {
      this.isShowing = false;
    }
  }

  constructor(private ref: ChangeDetectorRef, private formBuilder: FormBuilder, private apiService: ApiService, private router: Router, private route: ActivatedRoute, private matIconRegistry: MatIconRegistry,
    private domSanitizer: DomSanitizer) {
  }

  ngOnInit(): void {

    onAuthUIStateChange((authState, authData) => {
      this.authState = authState;
      this.user = authData as CognitoUserInterface;
      this.ref.detectChanges();
    });
    // this.filteredOptions = this.searchTextboxControl.valueChanges
    //   .pipe(
    //     startWith<string>(''),
    //     map(name => this._filter(name))
    //   );
  }

  ngOnDestroy() {
    return onAuthUIStateChange;
  }

  checkoutForm = this.formBuilder.group({
    name: '',
    email: '',
    password: '',
    mobno: '8800544771',
    exp_years: 10,
    exp_months: 5,
    resume: new FormControl('', [Validators.required]),
    fileSource: new FormControl('', [Validators.required]),
    terms_conditions: new FormControl('', [Validators.required])
  });

  private file: File;

  // title = 'training-app';
  test: String;

  onFileChange(event) {

    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.checkoutForm.patchValue({
        fileSource: file
      });
    }
  }

  onViewResume(event) {
    this.viewresume = true;
    this.updateresume = false;
    this.showskills = false;
    this.personaldetails = false;
  }
  onUpdateResume(event) {
    this.viewresume = false;
    this.updateresume = true;
    this.showskills = false;
    this.personaldetails = false;
  }
  onUpdateSkills(event) {
    this.viewresume = false;
    this.updateresume = false;
    this.showskills = true;
    this.personaldetails = false;
  }
  onUpdatePersonalDetails(event) {
    this.viewresume = false;
    this.updateresume = false;
    this.showskills = false;
    this.personaldetails = true;
  }
  onClickReview(event) {
    this.viewresume = true;
    this.updateresume = true;
    this.showskills = true;
    this.personaldetails = true;
  }
  onClickSubmit(): void {
    this.router.navigate(['register']);
    console.log(this.checkoutForm.get('terms_conditions').value);
    // Process checkout data here
    const formData = new FormData();
    formData.append('file', this.checkoutForm.get('fileSource').value);

    this.apiService.postFile(formData).subscribe((data) => {
      console.log(data);
    });


    console.warn('Your order has been submitted', this.checkoutForm.value.resume);
    this.apiService.getNews().subscribe((data) => {
      this.test = JSON.stringify(data)
      this.checkoutForm.patchValue({ name: this.test, email: '' });
    });

    this.apiService.postNews(JSON.stringify(this.checkoutForm.value)).subscribe((data) => {
      console.log(data);
    });

    console.log(JSON.stringify(this.checkoutForm.value))
  }

  /**
   * Used to filter data based on search input
   */
  private _filter(name: string): String[] {
    const filterValue = name.toLowerCase();
    // Set selected values to retain the selected checkbox state
    this.setSelectedValues();
    this.selectFormControl.patchValue(this.selectedValues);
    let filteredList = this.data.filter(option => option.toLowerCase().indexOf(filterValue) === 0);
    return filteredList;
  }

  /**
   * Remove from selected values based on uncheck
   */
  selectionChange(event) {
    if (event.isUserInput && event.source.selected == false) {
      let index = this.selectedValues.indexOf(event.source.value);
      this.selectedValues.splice(index, 1)
    }
  }

  openedChange(e) {
    // Set search textbox value as empty while opening selectbox
    this.searchTextboxControl.patchValue('');
    // Focus to search textbox while clicking on selectbox
    if (e == true) {
      this.searchTextBox.nativeElement.focus();
    }
  }

  /**
   * Clearing search textbox value
   */
  clearSearch(event) {
    event.stopPropagation();
    this.searchTextboxControl.patchValue('');
  }

  /**
   * Set selected values to retain the state
   */
  setSelectedValues() {
    console.log('selectFormControl', this.selectFormControl.value);
    if (this.selectFormControl.value && this.selectFormControl.value.length > 0) {
      this.selectFormControl.value.forEach((e) => {
        if (this.selectedValues.indexOf(e) == -1) {
          this.selectedValues.push(e);
        }
      });
    }
  }

}

