import {Component, OnInit} from '@angular/core';
import {Auth} from "aws-amplify";
import {CognitoUser} from "amazon-cognito-identity-js";
import {FormBuilder, FormControl, Validators} from "@angular/forms";
import {MatDialog} from "@angular/material/dialog";
import {AlertDialogComponent} from "../../util/alert-dialog/alert-dialog.component";
import {PasswordValidators} from "../../../util/validators.util";

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit {

  changePasswordForm = this.formBuilder.group({
    oldPassword: ['', {validators: [Validators.required]}],
    newPassword: ['', {validators: [Validators.required]}],
    confirmPassword: ['', {validators: [Validators.required]}],
  }, {validators: [PasswordValidators.validateConfirmPassword], updateOn: "change"})

  constructor(private formBuilder: FormBuilder,
              private matDialog: MatDialog) {
    //
  }

  get oldPasswordFormControl(): FormControl {
    return this.changePasswordForm.get(PasswordValidators.OLD_PASSWORD) as FormControl;
  }

  get newPasswordFormControl(): FormControl {
    return this.changePasswordForm.get(PasswordValidators.NEW_PASSWORD) as FormControl;
  }

  get confirmPasswordFormControl(): FormControl {
    return this.changePasswordForm.get(PasswordValidators.CONFIRM_PASSWORD) as FormControl;
  }

  private _errorMessage: string;

  get errorMessage(): string {
    return this.changePasswordForm?.errors?.['message'] || this._errorMessage;
  }

  ngOnInit(): void {
    //
  }

  async onClickChangePassword() {
    const values = this.changePasswordForm.value;
    await this.changePassword(values[PasswordValidators.OLD_PASSWORD], values[PasswordValidators.NEW_PASSWORD])
  }

  private async changePassword(oldPassword: string, newPassword: string) {
    try {
      const user: CognitoUser = await Auth.currentAuthenticatedUser();
      await Auth.changePassword(user, oldPassword, newPassword);
      this.matDialog.open(AlertDialogComponent, {data: {message: 'Password Changed'}});
      this.changePasswordForm.reset()
    } catch (e) {
      switch (e.code) {
        case 'NotAuthorizedException':
          this._errorMessage = 'incorrect password provided';
          break;
        case 'LimitExceededException':
        case 'InvalidPasswordException':
          this._errorMessage = e.message;
          break;
      }

    }
  }
}
