import { NumberInput } from '@angular/cdk/coercion';
import { Component, OnInit } from '@angular/core';

export interface subscription_data {
  plan_name: string
  subscription_id: string
  frequency: string
  no_of_tests: number
  no_of_interviews: number
  test_cam_on: boolean
  test_video_on: boolean
  interview_video_on: boolean
  per_test_price: number
  per_interview_price: number
  total_cost: number
  expiry_date: string
  status: string
  tests_conducted: number
  interviews_conducted: number
}

@Component({
  selector: 'app-view-subscription',
  templateUrl: './view-subscription.component.html',
  styleUrls: ['./view-subscription.component.css']
})
export class ViewSubscriptionComponent implements OnInit {
  subscriptionStatus = 'active';
  display_subscriptions = []
  subscriptions: subscription_data[] = [
    {
      plan_name: "GOLD",
      subscription_id: "xxxxx",
      frequency: "MONTHLY",
      no_of_tests: 200,
      no_of_interviews: 50,
      test_cam_on: false,
      test_video_on: true,
      interview_video_on: true,
      per_test_price: 200,
      per_interview_price: 2000,
      total_cost: 100000,
      expiry_date: "10-OCTOBER-2022",
      status: "active",
      tests_conducted: 15,
      interviews_conducted: 10
    },
    {
      plan_name: "GOLD",
      subscription_id: "yyyyyy",
      frequency: "SIX-MONTHLY",
      no_of_tests: 1300,
      no_of_interviews: 320,
      test_cam_on: false,
      test_video_on: true,
      interview_video_on: true,
      per_test_price: 200,
      per_interview_price: 2000,
      total_cost: 600000,
      expiry_date: "02-APR-2022",
      status: "expired",
      tests_conducted: 150,
      interviews_conducted: 100
    },
    {
      plan_name: "SILVER",
      subscription_id: "xxxxx",
      frequency: "MONTHLY",
      no_of_tests: 200,
      no_of_interviews: 20,
      test_cam_on: false,
      test_video_on: true,
      interview_video_on: false,
      per_test_price: 200,
      per_interview_price: 2000,
      total_cost: 60000,
      expiry_date: "01-APR-2022",
      status: "expired",
      tests_conducted: 15,
      interviews_conducted: 10
    },
  ]
  constructor() { }

  ngOnInit(): void {
    this.subscription_status_change()
  }

  subscription_status_change(){
    console.log(this.subscriptionStatus)
    this.display_subscriptions = []
    this.subscriptions.forEach(element => {
      if(element.status == this.subscriptionStatus){
        this.display_subscriptions.push(element)
      }
    });
  }
}


