import { Component, Input, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import {SpinnerVisibilityService} from "ng-http-loader";
import { ApiService } from 'src/app/api.service';

interface fetchinterviews {
  "role": string,
  "interview_id": number
}

@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.css']
})
export class FeedbackComponent implements OnInit{

  @Input() interview_id: number;

  complete_interview_details: any
  dtOptions_feedback: DataTables.Settings = {};
  dtTrigger_feedback: Subject<any> = new Subject<any>();
  feedbacktext = ""
  overall_feedback: number
  technical_feedback = [
    { skill: "", feedback: 0 }
  ]

  soft_feedback = [
    { name: "", score: 0 },
    { name: "", score: 0 },
    { name: "", score: 0 },
    { name: "", score: 0 }
  ]
  softskills = [
    "Logical reasoning",
    "Technical skills",
    "Communication",
    "Behaviour"
  ]
  constructor(private spinnerVisibilityService: SpinnerVisibilityService, private apiService: ApiService) { }

  ngOnInit(){
    this.initialize()
    this.fetchinterviews()
    // this.populatefeedback()
  }
  ngOnDestroy(): void {
    this.dtTrigger_feedback.unsubscribe();
  }

  ngOnChanges() {
    this.fetchinterviews()
  }

  populatefeedback(interview_id:number=this.interview_id) {

    let technical_feedback_temp = []
    let general_feedback_temp = []
    let feedback
    console.log(this.complete_interview_details, "complete details")
    this.complete_interview_details.forEach(element => {
      if (element.id == interview_id) {
        feedback = element.feedback
        console.log("matched", element.id)
      }
    });
    if (JSON.parse(feedback).technical.length > 0) {
      JSON.parse(feedback).technical.forEach(element => {
        technical_feedback_temp.push({ skill: element.name, feedback: element.score })
      });
    }
    if (JSON.parse(feedback).general.length > 0) {
      JSON.parse(feedback).general.forEach(element => {
        general_feedback_temp.push({ name: element.name, score: element.score })
      });
    }
    technical_feedback_temp.length > 0 ? this.technical_feedback = technical_feedback_temp : ""
    general_feedback_temp.length > 0 ? this.soft_feedback = general_feedback_temp : ""
    console.log(this.soft_feedback)
    this.overall_feedback = JSON.parse(feedback).overall_rating
    this.feedbacktext = JSON.parse(feedback).feedback
    console.log(this.technical_feedback)
  }

  fetchinterviews() {
    console.log(this.interview_id)
    let interviewdata: fetchinterviews = {
      "role": "recruiter",
      "interview_id": this.interview_id
    }
    this.spinnerVisibilityService.show();
    this.apiService.fetch_interviews(interviewdata).then((data) => {
      console.log(data)
      this.complete_interview_details = data  
      this.populatefeedback()
      // this.spinnerVisibilityService.hide();
    }).finally(() => {
      this.spinnerVisibilityService.hide();
    });

  }

  initialize(){
    this.complete_interview_details = null
    this.feedbacktext = ""
    this.overall_feedback = null
    this.technical_feedback = [
      { skill: "", feedback: 0 }
    ]
  }
}
