  fetch_schedule_match() {
    // this.schedule_new = [
    //   {
    //     candidate_id: "38",
    //     interviewer_id: "39",
    //     datetime: [
    //       { slot: "2022-01-25 15:30:00", id: 1 },
    //       { slot: "2022-01-25 15:30:00", id: 1 },
    //       { slot: "2022-01-25 15:30:00", id: 1 }
    //     ]
    //   }
    // ]
    this.schedule_new = []
    let distinct_candidates = []
    let distinct_candidates_count = 0
    let candidate_count = 0
    let found
    this.spinnerVisibilityService.show();
    this.apiService.fetchinterviewmatches().then((data) => {
      console.log(data)
      data.forEach(element => {
        let input = {
          reference_id: element.reference_id
        }
        this.apiService.fetch_reference_details(input).then((reference_data) => {
          found = false
          if (distinct_candidates.length > 0) {
            distinct_candidates.forEach(distinct => {
              if (element.candidate_id == distinct.candidate_id && element.reference_id == distinct.reference_id) {
                this.schedule_new[distinct.slot].datetime.push({ slot: element.schedule, id: element.id })
                found = true
              }
            });
          }
          if (!found) {
            this.schedule_new[distinct_candidates_count] =
              {
                candidate_id: element.candidate_id,
                interviewer_id: element.interviewer_id,
                datetime: [
                  { slot: element.schedule, id: element.id },
                ],
                reference_id: element.reference_id,
                interview_skills: element.interview_skills,
                reference_skills: JSON.parse(reference_data[0].skills_and_count).skill_details
              }
              distinct_candidates_count = distinct_candidates_count + 1
            distinct_candidates.push({ slot: candidate_count, candidate_id: element.candidate_id, reference_id: element.reference_id })
            candidate_count = candidate_count + 1

          }
        })
      });
    }).finally(() => {
      this.spinnerVisibilityService.hide();
    });

  }

  schedule_interview(candidate_id: any, reference_id: any) {
    let interview_data: interview_data = {
      schedule: this.chosen_slot,
      candidate_id: candidate_id,
      status: "scheduled",
      feedback: {},
      remarks: {
        id: "",
        cancelled_by: "",
        cancelreason: "",
        video_status: "pending",
        payment_status: "pending",
        feedback_status: "pending"
      },
      reference_id: reference_id
    }
    this.spinnerVisibilityService.show();
    this.apiService.schedule_interview(interview_data).then((data) => {
      console.log(data)
      this.openDialog("Interview scheduled successfully", "success")
      this.fetch_schedule_match()
    }).finally(() => {
      this.spinnerVisibilityService.hide();
    });

  }
  skills_feedback(i, rating) {
    this.technical_feedback[i].feedback = rating
    i >= this.technical_feedback.length - 1 ? this.technical_feedback.push({ skill: "", feedback: 0 }) : ""
    let index = this.candidate_skills.indexOf(this.technical_feedback[i].skill, 0);
    // this.candidate_skills.splice(index, 1);
    console.log("index", index)
    console.log(this.technical_feedback)
  }
  softfeedback(i, rating) {
    // this.soft_feedback.push({ name: this.softskills[i], score: rating })
    this.soft_feedback[i].score = rating
    console.log("soft skills", this.soft_feedback)
  }

  overallfeedback(rating) {
    this.overall_feedback = rating
  }
  submitfeedback() {
    console.log(this.interview_details.id)
    let complete_feedback: complete_feedback = {
      interview_id: 0,
      feedback: "",
      technical: [
        {
          name: "",
          score: 0
        }
      ],
      general: [
        {
          name: "",
          score: 0
        }
      ],
      overall_rating: 0,
      attended: false
    }
    complete_feedback.interview_id = this.interview_details.id
    complete_feedback.feedback = this.feedbacktext
    complete_feedback.technical.pop()
    this.technical_feedback.forEach(element => {
      complete_feedback.technical.push({ name: element.skill, score: element.feedback })
    });
    complete_feedback.general.pop()
    this.soft_feedback.forEach(element => {
      complete_feedback.general.push({ name: element.name, score: element.score })
    });
    complete_feedback.attended = true      //change this to real input
    complete_feedback.overall_rating = this.overall_feedback
    console.log(complete_feedback)
    this.spinnerVisibilityService.show();
    this.apiService.submitfeedback(complete_feedback).then((data) => {
      console.log(data)
      this.fetchinterviews()
      this.openDialog("Feedback submitted successfully", "success")
    }).finally(() => {
      this.spinnerVisibilityService.hide();
    });
  }

  populatefeedback() {
    let interview_id = this.interview_details.id
    let technical_feedback_temp = []
    let general_feedback_temp = []
    console.log(this.interview_details.id, "interview details id")
    let feedback
    console.log(this.complete_interview_details, "complete details")
    this.complete_interview_details.forEach(element => {
      if (element.id == interview_id) {
        feedback = element.feedback
        console.log("matched", element.id)
      }
    });
    if (JSON.parse(feedback).technical.length > 0) {
      JSON.parse(feedback).technical.forEach(element => {
        technical_feedback_temp.push({ skill: element.name, feedback: element.score })
      });
    }
    if (JSON.parse(feedback).general.length > 0) {
      JSON.parse(feedback).general.forEach(element => {
        general_feedback_temp.push({ name: element.name, score: element.score })
      });
    }
    this.technical_feedback = technical_feedback_temp
    this.soft_feedback = general_feedback_temp
    this.overall_feedback = JSON.parse(feedback).overall_rating
    this.feedbacktext = JSON.parse(feedback).feedback
  }

