import {Component, OnInit} from '@angular/core';
import {ReferenceIdSummary, TestService} from "../../../../services/test/test.service";
import {Summary} from "../../../../models/test.model";
import {FormControl, Validators} from "@angular/forms";

@Component({
  selector: 'candidate-test',
  templateUrl: './test.component.html',
  styleUrls: ['./test.component.css']
})
export class TestComponent implements OnInit {

  summaries: Summary[];
  referenceIdForm: FormControl = new FormControl('', {validators: Validators.required});
  referenceIdSummary: ReferenceIdSummary = {};
  private testURL = '';

  public get errorMessage() {
    const errorMessage = this.referenceIdSummary.errorMessage;

    if (errorMessage && errorMessage.startsWith("not enough questions available")) {
      return "Invalid reference Id, not enough questions";
    } else {
      return errorMessage;
    }
  }

  constructor(private testService: TestService) {
    //
  }

  get noOfAttempts(): number {
    return this.summaries?.length;
  }

  ngOnInit(): void {
    //
  }

  async onSummaryTab() {
    try {
      this.summaries = await this.testService.getAnswers()
    } catch (e) {
      console.error(e);
    }
  }

  startTest() {
    if (this.testURL !== null) {
      window.open(this.testURL, '_blank', 'fullscreen=yes');
    }
  }

  async checkValidateReferenceId() {
    this.referenceIdSummary = await this.testService.validateReferenceId(this.referenceIdForm.value);
    if (this.referenceIdSummary.errorMessage !== null) {
      this.testURL = `/test/${btoa(JSON.stringify({
        referenceId: this.referenceIdForm.value
      }))};`
    } else {
      this.testURL = null;
    }
  }
}

