import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Question} from "../../../models/test.model";


@Component({
  selector: 'test-answer',
  templateUrl: './answer.component.html',
  styleUrls: ['./answer.component.css']
})
export class AnswerComponent implements OnInit {
  question_image_path = ""
  @Input('allQuestions') allQuestions: Question[];
  @Output('reviewCompleteEvent') reviewCompleteEvent = new EventEmitter<void>();
  currentQuestion: number = 0;

  constructor() {
    //
  }

  ngOnInit(): void {
    //
  }

  search_view(activeTab) {
    this.currentQuestion = activeTab;
  }

  rightAnswer(question: Question, option: string) {
    return question.answer === option;
  }

  wrongAnswer(question: Question, option: string) {
    return question.selectedAnswer.answer === option && question.answer !== option;
  }

  reviewAnswerComplete() {
    this.reviewCompleteEvent.emit();
  }
  split_text(text:string){
    console.log(text.split(":")[1])
    if (text.split(":")[0] == "IMAGE"){
      this.question_image_path = "assets/assessment/questions/images/psychometric/" + text.split(":")[1] + ".jpg"
      console.log(this.question_image_path)
      return text.split(":")[1]
    }else{
      this.question_image_path=""
      return ""
    }
  }
}
