import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {
  EMAIL,
  EXPERIENCE,
  FULL_NAME,
  LOCATION,
  MOBILE_NUMBER,
  OPEN_FOR_CHANGE,
  SALARY
} from "../../../../models/constants";
import {Profile, SalariesEnum} from "../../../../models/candidate.model";
import {PersonalDetailsService} from "../../../../services/candidate/my-profile/personal-details.service";
import {MatDialog} from "@angular/material/dialog";
import {AlertDialogComponent} from "../../../util/alert-dialog/alert-dialog.component";
import {CountryISO, PhoneNumberFormat, SearchCountryField} from "ngx-intl-tel-input";

@Component({
  selector: 'candidate-personal-details',
  templateUrl: './personal-details.component.html',
  styleUrls: ['./personal-details.component.css']
})
export class PersonalDetailsComponent implements OnInit {

  profileForm: FormGroup = this.formBuilder.group({
    fullName: ['', [Validators.minLength(2)]],
    location: [''],
    mobileNumber: this.formBuilder.control('',
      {
        validators: [Validators.minLength(5), Validators.required]
      }),
    emailAddress: ['', [Validators.email]],
    experience: [''],
    salary: [''],
    openForChange: [''],
  });

  readonly SearchCountryField = SearchCountryField;
  readonly CountryISO = CountryISO;
  readonly PhoneNumberFormat = PhoneNumberFormat;
  readonly preferredCountries: CountryISO[] = [CountryISO.India, CountryISO.UnitedStates, CountryISO.UnitedKingdom];

  constructor(private formBuilder: FormBuilder,
              private personalDetailService: PersonalDetailsService,
              private matDialog: MatDialog) {
  }

  get fullName() {
    return this.profileForm.get(FULL_NAME)
  }

  get location() {
    return this.profileForm.get(LOCATION)
  }

  get mobileNumber(): FormControl {
    return this.profileForm.get(MOBILE_NUMBER) as FormControl;
  }

  get email() {
    return this.profileForm.get(EMAIL)
  }

  get experience() {
    return this.profileForm.get(EXPERIENCE).value
  }

  get salary() {
    return this.profileForm.get(SALARY).value
  }

  get salariesEnum() {
    return Object.keys(SalariesEnum);
  }

  get openForChange() {
    return this.profileForm.get(OPEN_FOR_CHANGE);
  }


  ngOnInit(): void {
    this.profileForm.disable()
    this.personalDetailService.fetchPersonalDetails()
      .then(d => this.initProfileForm(d));
  }

  public enableEdit() {
    this.profileForm.enable()
    this.profileForm.get(EXPERIENCE).enable();
    this.profileForm.get(SALARY).enable();
  }

  public async updateDetails() {
    const profile: Profile = this.profileForm.value
    profile.mobileNumber = profile.mobileNumber['e164Number'];
    await this.personalDetailService.updatePersonalDetails(profile);
    this.matDialog.open(AlertDialogComponent, {data: {message: 'Details Updated!'}});
    this.profileForm.disable()
  }

  changeOpenForChange(status: boolean) {
    this.profileForm.get(OPEN_FOR_CHANGE).setValue(status);
  }

  private initProfileForm(userProfile: Profile) {
    this.profileForm.setValue({...userProfile});
  }

}
