import {Component, Input, OnInit} from '@angular/core';
import {ReferenceAgenciesService} from "../../../services/reference-agencies/reference-agencies.service";
import {AbstractControl} from "@angular/forms";

@Component({
  selector: 'app-reference-select',
  templateUrl: './reference-select.component.html',
  styleUrls: ['./reference-select.component.css']
})
export class ReferenceSelectComponent implements OnInit {

  @Input('referenceFormControl') referenceFormControl: AbstractControl;
  @Input('referenceId') agencyId: number;
  @Input('requiredMarker') requiredMarker: boolean = false;

  referenceAgencies: { id: number, name: string }[];

  constructor(private referenceAgenciesService: ReferenceAgenciesService,) {
  }

  ngOnInit(): void {
    this.referenceAgenciesService.getReferenceAgencies()
      .then(data => this.referenceAgencies = data);
  }

}
