import {Component, OnInit, ViewChild} from '@angular/core';
import {MyProfileEnum, MyProfileView} from "../../../models/candidate.model";
import {ProfileCardComponent} from "../../util/profile-card/profile-card.component";

@Component({
  selector: 'candidate-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  myProfileView: MyProfileView;
  @ViewChild('profileCard') profileCard: ProfileCardComponent;

  constructor() {
    //
  }

  ngOnInit(): void {
    this.myProfileView = {personalDetails: true};
  }

  public updateView(view: MyProfileEnum) {
    switch (view) {
      case MyProfileEnum.PROFILE:
        this.myProfileView = {personalDetails: true};
        break;
      case MyProfileEnum.SKILLS:
        this.myProfileView = {skills: true};
        break;
      case MyProfileEnum.RESUME:
        this.myProfileView = {resume: true};
        break;
    }
  }

  handleTaglineChangeEvent($event: string) {
    this.profileCard._tagline = $event;
  }
}
