import {Component, OnInit} from '@angular/core';
import {AuthState, CognitoUserInterface} from "@aws-amplify/ui-components";
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {HeaderUpdateEvent, HeaderUpdateService} from "../../../services/header/header-update.service";
import {Router} from "@angular/router";
import {SpinnerVisibilityService} from "ng-http-loader";
import {Auth} from "aws-amplify";
import {Roles} from "../../../models/auth.model";
import {AUTH_GET_ID_TOKEN} from "../../../models/auth.constants";

@Component({
  selector: 'app-candidate-sign-in',
  templateUrl: './candidate-sign-in.component.html',
  styleUrls: ['./candidate-sign-in.component.css']
})
export class CandidateSignInComponent implements OnInit {

  user: CognitoUserInterface | undefined;
  authState!: AuthState;
  redirectURL: any;

  signInForm: FormGroup = this.formBuilder.group({
    userName: this.formBuilder.control('', {validators: [Validators.required]}),
    password: this.formBuilder.control('', {validators: [Validators.required]}),
  })

  error: Error = null;

  constructor(private formBuilder: FormBuilder,
              private headerUpdateService: HeaderUpdateService,
              private router: Router,
              private spinnerVisibilityService: SpinnerVisibilityService) {
  }


  get userNameFormControl(): FormControl {
    return this.signInForm.get('userName') as FormControl;
  }

  get passwordFormControl(): FormControl {
    return this.signInForm.get('password') as FormControl;
  }

  ngOnInit(): void {
    //
  }

  login() {
    const values = this.signInForm.value;
    this.spinnerVisibilityService.show();
    Auth.signIn(values.userName, values.password)
      .then(() => this.fetchRoles())
      .then(_ => this.error = null)
      .catch(err => {
        this.error = err;
        console.error(this.error.name, this.error.message);
      })
      .finally(() => {
        this.spinnerVisibilityService.hide();
      })
  }

  fetchRoles(): Promise<void> {
    return AUTH_GET_ID_TOKEN()
      .then(token => {
        const groups = token["cognito:groups"];
        const role = groups[0].toUpperCase();
        this.redirectBasedOnRoles(role);
        this.headerUpdateService.sendUpdate(HeaderUpdateEvent.SIGN_IN);
      })
  }

  private redirectBasedOnRoles(role: any) {
    switch (role) {
      case Roles.ADMIN:
        this.router.navigate(['/home']).catch(err => console.log(err));
        break;
      case Roles.CANDIDATE:
        this.router.navigate(['/candidate/profile']).catch(err => console.log(err));
        break;
      case Roles.INTERVIEWER:
        this.router.navigate(['/interviewer/profile']).catch(err => console.log(err));
        break;
      case Roles.RECRUITER:
        this.router.navigate(['/recruiter/profiles']).catch(err => console.log(err));
        break;
    }

  }


}
