import {Component, ElementRef, OnInit, Renderer2, ViewChild} from '@angular/core';
import {HeaderUpdateEvent, HeaderUpdateService} from "../../../services/header/header-update.service";
import {AUTH_GET_ID_TOKEN} from "../../../models/auth.constants";
import {ProfileImageService} from "../../../services/candidate/my-profile/profile-image.service";
import {SkillsService} from "../../../services/candidate/my-profile/skills.service";

@Component({
  selector: 'app-profile-card',
  templateUrl: './profile-card.component.html',
  styleUrls: ['./profile-card.component.css']
})
export class ProfileCardComponent implements OnInit {

  @ViewChild('profileImage', {static: true}) profileImage: ElementRef<HTMLImageElement>;

  fullName: string;
  tagline: string;

  constructor(private skillsService: SkillsService,
              private profileImageService: ProfileImageService,
              private renderer: Renderer2,
              private headerUpdateService: HeaderUpdateService) {
  }

  set _tagline(tagline: string) {
    this.tagline = tagline;
  }

  ngOnInit(): void {
    this.updateImage()
    this.skillsService.getUserSkillsAndTagline()
      .then(data => data.tagline)
      .then(tagline => this.tagline = tagline?.name)
    AUTH_GET_ID_TOKEN().then(token => this.fullName = token.full_name);
  }

  async uploadProfileImage(files: FileList) {
    if (files.length !== 1) {
      return;
    }
    const file: File = files[0];
    await this.profileImageService.uploadProfileImage(file);
    this.headerUpdateService.sendUpdate(HeaderUpdateEvent.IMAGE_CHANGE);
    this.updateImage()
  }

  private updateImage() {
    this.profileImageService.getProfileImage()
      .then(img => this.renderer.setAttribute(this.profileImage.nativeElement, 'src', img))
  }

}
