import {Injectable} from '@angular/core';
import {Skills, SkillsTagLine, TaglineAndSkills, UpdateTaglineAndSkills} from "../../../models/candidate.model";
import {END_POINT, HTTP_HEADERS} from "../../../models/constants";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {firstValueFrom} from "rxjs";
import {USER_ID} from "../../../models/auth.constants";

@Injectable({
  providedIn: 'root'
})
export class SkillsService {

  constructor(private http: HttpClient) {
  }

  public async patchUserSkills(update_skills: UpdateTaglineAndSkills) {
    const URL = `${END_POINT}/v1/candidate/tagline-and-skills`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    return firstValueFrom(this.http.put(URL, update_skills, {headers: httpHeaders}));
  }


  public async getAllTagLines(): Promise<Array<SkillsTagLine>> {
    const URL = `${END_POINT}/v1/candidate/skill-taglines`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    return firstValueFrom(this.http.get<Array<SkillsTagLine>>(URL, {headers: httpHeaders}));
  }

  public async getAllSkills(): Promise<Array<Skills>> {
    const URL = `${END_POINT}/v1/candidate/skills`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    return firstValueFrom(this.http.get<Array<Skills>>(URL, {headers: httpHeaders}));
  }

  public async getUserSkillsAndTagline(): Promise<TaglineAndSkills> {
    const URL = `${END_POINT}/v1/candidate/tagline-and-skills`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    return firstValueFrom(this.http.get<TaglineAndSkills>(URL, {headers: httpHeaders}));
  }

}
