import {Injectable} from "@angular/core";
import {firstValueFrom} from "rxjs";
import {END_POINT} from "../../models/constants";
import {HttpClient} from "@angular/common/http";

@Injectable({
  providedIn: "root",
})
export class AuthService {
  constructor(private http: HttpClient) {
  }

  public async userNameAvailable(userName: string): Promise<boolean> {
    const URL = `${END_POINT}/v1/auth/user-name-available?user-name=${userName}`;
    const resp = await firstValueFrom(this.http.get<{ string: boolean }>(URL));
    return resp[userName];
  }

  public async resetPassword(userName: string): Promise<boolean>{
    const URL = `${END_POINT}/v1/user/password/reset`;
    try{
    const resp = await firstValueFrom(this.http.post(URL, {userName: userName}))
      return true;
    }catch (err){
      console.error(err);
      return false;
    }
  }

}
