import {Component, EventEmitter, HostListener, Input, OnInit, Output, ViewChild} from '@angular/core';
import {GetQuestionResponse, Question} from "../../../models/test.model";
import {CountdownConfig, CountdownEvent} from "ngx-countdown/interfaces";
import {TestService} from "../../../services/test/test.service";
import {CountdownComponent} from "ngx-countdown";
import BlurEvent = JQuery.BlurEvent;

@Component({
  selector: 'test-question',
  templateUrl: './question.component.html',
  styleUrls: ['./question.component.css']
})
export class QuestionComponent implements OnInit {

  question_image_path = ""
  @Input() questions: Question[];
  @Output() questionsChange = new EventEmitter<Question[]>();
  @Input() totalDuration: string;
  @Input() referenceId: string;
  @Output() totalDurationChange = new EventEmitter<string>();
  @Output('endTest') endTest = new EventEmitter<string>();
  timerConfig: CountdownConfig = {leftTime: 10, notify: 0, demand: true};
  currentQuestion = 0;
  @ViewChild('countDown') countDown: CountdownComponent;
  @Input() testId: string;
  @Output() testIdChange = new EventEmitter<string>();
  recordVideo: boolean;
  captureImage: boolean;
  private durations: number;

  constructor(private testService: TestService) {
  }

  async ngOnInit(): Promise<void> {

    try {
      const response: GetQuestionResponse = await this.testService.getQuestions(this.referenceId);
      this.changeTestId(response.testId)
      this.changeQuestions(response.questions);
      this.durations = response.duration * 60;
      this.setCountdownTimer(this.durations);
      this.recordVideo = response.isVideoEnabled;
      this.captureImage = response.isImageEnabled;
    } catch (err) {
      console.error(err);
    }
  }

  search(activeTab) {
    this.currentQuestion = activeTab;
  }

  answerSelected(question: Question, answer: string) {
    question.selectedAnswer = {
      questionId: question.id,
      skillId: question.skillId,
      answer: answer,
      isCorrectAnswer: answer === question.answer,
    }
  }

  durationInMmSs(value: number): string {
    if (isNaN(value)) {
      return "00:00";
    }
    const minutes: number = Math.floor(value / 60);
    return minutes.toString().padStart(2, '0') + ':' +
      (value - minutes * 60).toString().padStart(2, '0');
  }

  _endTest(message: string) {
    this.timerConfig = {
      leftTime: this.durations,
      notify: 0,
      demand: true
    };
    this.endTest.emit(message);
  }

  finishTest(e: CountdownEvent) {
    this.updateDetails(this.questions, e['left'] / 1000)
    if (e["action"] == "done") {
      this._endTest("Time Up!");
    }
  }

  updateDetails(questions: Question[], durationLeft: number) {
    this.changeQuestions(questions);
    this.changeTotalDuration(durationLeft)
  }

  private changeTestId(testId: string) {
    this.testId = testId;
    this.testIdChange.emit(testId);
  }

  private setCountdownTimer(time: number) {
    const config = this.countDown.config;
    config.leftTime = time;
    this.countDown.config = config;
    this.countDown.restart()
    this.countDown.begin();
  }

  private changeTotalDuration(durationLeft: number) {
    const totalDuration: string = this.durationInMmSs((this.durations - durationLeft))
    this.totalDuration = totalDuration;
    this.totalDurationChange.emit(totalDuration);
  }

  private changeQuestions(value: Question[]) {
    this.questions = value;
    this.questionsChange.emit(value);
  }

  @HostListener('window:keydown', ['$event'])
  private keydownEvent(event: KeyboardEvent) {
    this._endTest("please do not use your keyboard")
    event.preventDefault();
  }

  @HostListener("window:blur", ['$event'])
  private focusEvent(event: BlurEvent) {
    this._endTest("please do not switch screens.");
    event.preventDefault();
  }
  split_text(text:string){
    console.log(text.split(":")[1])
    if (text.split(":")[0] == "IMAGE"){
      this.question_image_path = "assets/assessment/questions/images/psychometric/" + text.split(":")[1] + ".jpg"
      console.log(this.question_image_path)
      return text.split(":")[1]
    }else{
      this.question_image_path=""
      return ""
    }
  }

}
