import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Question} from "../../../models/test.model";
import {TestComponent} from "../test/test.component";

@Component({
  selector: 'test-summary',
  templateUrl: './summary.component.html',
  styleUrls: ['./summary.component.css']
})
export class SummaryComponent implements OnInit {

  @Input() questions: Question[];
  @Output('viewAnswer') viewAnswer = new EventEmitter<void>();
  totalAnswered: number;
  rightAnswered: number;
  wrongAnswered: number;
  @Input() totalTestDuration: string;

  constructor() {
    //
  }

  get testScore(): number {
    return parseFloat((this.rightAnswered / this.questions?.length * 100).toFixed(2));
  }

  private static answerSummary(questions: Question[]): { total: number, right: number, wrong: number } {
    let questions_answered = 0
    let right_answered = 0
    let wrong_answered = 0

    for (const question of questions) {
      if (question.selectedAnswer.answer !== '') {
        questions_answered++;
        question.selectedAnswer.answer === question.answer ? right_answered++ : wrong_answered++;
      }
    }
    return {total: questions_answered, right: right_answered, wrong: wrong_answered};
  }

  ngOnInit(): void {
    const answerSummary = SummaryComponent.answerSummary(this.questions);
    this.rightAnswered = answerSummary.right;
    this.wrongAnswered = answerSummary.wrong;
    this.totalAnswered = answerSummary.total;
  }

  _viewAnswers() {
    this.viewAnswer.emit();
  }

  closeWindow() {
    window.close();
    TestComponent.closeFullscreen()
  }

}
