import {Injectable} from '@angular/core';
import {Observable, Subject} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class FooterUpdateService {

  private subjectName = new Subject<FooterUpdateEvent>(); //need to create a subject

  constructor() {
  }


  sendUpdate(message: FooterUpdateEvent) { //the component that wants to update something, calls this fn
    this.subjectName.next(message); //next() will feed the value in Subject
  }

  getUpdate(): Observable<FooterUpdateEvent> { //the receiver component calls this function
    return this.subjectName.asObservable(); //it returns as an observable to which the receiver function will subscribe
  }
}

export enum FooterUpdateEvent {
  START_TEST = "START_TEST",
  END_TEST = "END_TEST",
}
