import {Auth} from "aws-amplify";
import jwt_decode from "jwt-decode";
import {IdToken, Roles} from "./auth.model";
import {CognitoIdToken, CognitoUserSession} from "amazon-cognito-identity-js";

export const AUTH_GET_SESSION = async (): Promise<CognitoUserSession> => {
  return Auth.currentSession();
}

export const AUTH_GET_AWS_ID_TOKEN = async (): Promise<CognitoIdToken> => {
  const data = await AUTH_GET_SESSION();
  return data.getIdToken();
}

export const AUTH_GET_JWT_TOKEN = async (): Promise<string> => {
  const data = await AUTH_GET_AWS_ID_TOKEN();
  return data.getJwtToken();
}

export const AUTH_GET_ID_TOKEN = async (): Promise<IdToken> => {
  const data = await AUTH_GET_JWT_TOKEN();
  return jwt_decode<IdToken>(data);
}

export const USER_ID = async (): Promise<number> => {
  const data = await AUTH_GET_ID_TOKEN();
  return data.user_id;
}

export const GET_FULL_NAME = async (): Promise<string> => {
  const data = await AUTH_GET_ID_TOKEN();
  return data.full_name;
}

export const IS_SESSION_VALID = async (): Promise<boolean> => {
  try {
    const data = await AUTH_GET_SESSION();
    return data.isValid();
  } catch (e) {
    return false;
  }
}

export const HAS_ROLE = async (roleToCheck: Roles): Promise<boolean> => {
  try {
    const idToken = await AUTH_GET_ID_TOKEN();
    const role = idToken["cognito:groups"][0]?.toUpperCase();
    return Roles[role] === roleToCheck;
  } catch (e) {
    console.error(e);
    return false;
  }
}
