import {Component, HostListener, OnDestroy, OnInit} from '@angular/core';
import {HeaderUpdateEvent, HeaderUpdateService} from "../../../services/header/header-update.service";
import {FooterUpdateEvent, FooterUpdateService} from "../../../services/footer/footer-update.service";
import {ApiService} from "../../../app/api.service";
import {Question, TestView} from "../../../models/test.model";
import {TestService} from "../../../services/test/test.service";
import {ActivatedRoute, Router} from "@angular/router";
import {firstValueFrom} from "rxjs";
import BlurEvent = JQuery.BlurEvent;
import ContextMenuEvent = JQuery.ContextMenuEvent;


@Component({
  selector: 'app-test',
  templateUrl: './test.component.html',
  styleUrls: ['./test.component.css']
})
export class TestComponent implements OnInit, OnDestroy {

  private static readonly MAX_NUMBER_OF_WARNINGS = -1;

  questions: Question[];
  totalDuration: string = "00:00";
  view: TestView;
  testId: string;
  referenceId: string
  private numberOfWarnings: number = 0;
  private answersSaved = false;

  constructor(private headerUpdateService: HeaderUpdateService,
              private footerUpdateService: FooterUpdateService,
              private apiService: ApiService,
              private testService: TestService,
              private router: Router,
              private activatedRoute: ActivatedRoute) {
  }

  public static openFullscreen() {
    const elem: any = document.documentElement;
    if (elem.requestFullscreen) {
      elem.requestFullscreen();
    } else if (elem.webkitRequestFullscreen) { /* Safari */
      elem.webkitRequestFullscreen();
    } else if (elem.msRequestFullscreen) { /* IE11 */
      elem.msRequestFullscreen();
    }
  }

  public static async closeFullscreen() {
    try {
      const doc: any = document;
      if (doc.exitFullscreen) {
        await doc.exitFullscreen();
      } else if (doc.webkitExitFullscreen) { /* Safari */
        await doc.webkitExitFullscreen();
      } else if (doc.msExitFullscreen) { /* IE11 */
        await doc.msExitFullscreen();
      }
    } catch (err) {
      if (err.message === "Failed to execute 'exitFullscreen' on 'Document': Document not active") {
        return;
      }
      console.error(err);
    }
  }

  cancelTest() {
    window.close();
  }

  ngOnDestroy(): void {
    this.headerUpdateService.sendUpdate(HeaderUpdateEvent.END_TEST);
    this.footerUpdateService.sendUpdate(FooterUpdateEvent.END_TEST);
  }

  ngOnInit(): void {
    if (window.opener === null) {
      window.open('/test', '_blank', 'fullscreen=yes');
      this.router.navigate(['/']).catch(err => console.error(err));  // TODO change where to redirect to in case of accessed test directly
    }
    firstValueFrom(this.activatedRoute.paramMap)
      .then(params => {
          const data = JSON.parse(atob(params.get("data")));
          this.referenceId = data.referenceId;
        }
      );
    this.view = {startTest: true};
    this.headerUpdateService.sendUpdate(HeaderUpdateEvent.START_TEST);
    this.footerUpdateService.sendUpdate(FooterUpdateEvent.START_TEST);
  }

  reviewAnswerComplete() {
    this.view = {summary: true};
  }

  endTest() {
    this.view = {summary: true};
    if (!this.answersSaved) {
      const selectedAnswers = this.questions.map(question => question.selectedAnswer);
      this.testService.saveAnswers(selectedAnswers, this.testId, this.referenceId)
        .catch(err => console.error(err));
      this.answersSaved = true;
    }
  }

  enableQuestionsView() {
    this.view = {startTest: true}
  }

  async startTest() {
    this.view = {test: true}
    TestComponent.openFullscreen();
  }

  viewAnswers() {
    this.view = {answers: true}
  }

  warnAndEndTest(message: string) {
    if (this.view.test) {
      if (++this.numberOfWarnings > TestComponent.MAX_NUMBER_OF_WARNINGS) {
        this.endTest();
      } else {
        alert(message)
      }
    }
  }

  @HostListener("document:fullscreenchange", ['$event'])
  private fullScreen(event: KeyboardEvent) {
    if (!document.fullscreenElement) {
      this.warnAndEndTest("please do not resize screen")
      event.stopImmediatePropagation();
      event.stopPropagation();
      event.preventDefault();
    }
  }

  @HostListener('contextmenu', ['$event'])
  private onRightClick(event: ContextMenuEvent) {
    event.preventDefault();
  }

  @HostListener('window:keydown', ['$event'])
  private keydownEvent(event: KeyboardEvent) {
    event.preventDefault();
  }

  @HostListener('window:keyup', ['$event'])
  private keyupEvent(event: KeyboardEvent) {
    event.preventDefault();
  }

  @HostListener("window:blur", ['$event'])
  private focusEvent(event: BlurEvent) {
    console.info("user moved out from test window", event)
  }
}
