import { Component, Input, OnInit } from '@angular/core';
import { SpinnerVisibilityService } from 'ng-http-loader';
import { Subject } from 'rxjs';
import { ApiService } from 'src/app/api.service';

interface skillInput {
  testid:number
}

@Component({
  selector: 'app-skills-util',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.css']
})
export class SkillsComponentUtil implements OnInit {
 skillInput: skillInput = {testid: null}
  @Input() test_id: number= 13;
  dtOptions_feedback: DataTables.Settings = {};
  dtTrigger_feedback: Subject<any> = new Subject<any>();
  
  skills = [
    {name:"Angular",score:40},
    {name:"Core Java",score:30}
  ]

  constructor(private spinnerVisibilityService: SpinnerVisibilityService, private apiService: ApiService) { }

  ngOnInit(): void {
    this.skillInput.testid = this.test_id
    this.fetchSkillScores()
  }

  ngOnChanges() {
    this.skillInput.testid = this.test_id
    this.fetchSkillScores()
  }
  
  fetchSkillScores(){
    this.spinnerVisibilityService.show();
    this.apiService.fetch_skill_scores(this.skillInput).then((data) => {
      this.skills = data  
      // this.spinnerVisibilityService.hide();
    }).finally(() => {
      this.spinnerVisibilityService.hide();
    });
  }

}
