import {Injectable} from '@angular/core';
import {HttpClient, HttpErrorResponse, HttpHeaders} from "@angular/common/http";
import {END_POINT, HTTP_HEADERS} from "../../models/constants";
import {firstValueFrom} from "rxjs";
import {Answer, GetQuestionResponse, Question, Summary} from "../../models/test.model";

@Injectable({
  providedIn: 'root'
})
export class TestService {

  constructor(private http: HttpClient) {
  }

  public async getQuestions(referenceId: string): Promise<GetQuestionResponse> {
    const URL = `${END_POINT}/v1/test/questions?referenceId=${referenceId}`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    const response = await firstValueFrom(this.http.get<GetQuestionResponse>(URL, {headers: httpHeaders}));
    response.questions = response.questions.map(question => {
      question.selectedAnswer = {
        questionId: question.id,
        answer: '',
        isCorrectAnswer: false,
        skillId: question.skillId
      }
      return question;
    });
    return response;
  }

  public async saveAnswers(answers: Answer[], testId: string, referenceId: string) {
    const URL = `${END_POINT}/v1/test/answers`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    return firstValueFrom(this.http.post<Question[]>(URL, {answers, testId, referenceId}, {headers: httpHeaders}))
  }

  public async getAnswers(limit: number = 999): Promise<Summary[]> {
    const URL = `${END_POINT}/v1/test/answers?limit=${limit}`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    return firstValueFrom(this.http.get<Summary[]>(URL, {headers: httpHeaders}))
  }

  public async validateReferenceId(referenceId: string): Promise<ReferenceIdSummary> {
    const URL = `${END_POINT}/v1/test/reference/validate?referenceId=${referenceId}`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    try {
      return await firstValueFrom(this.http.get<ReferenceIdSummary>(URL, {headers: httpHeaders}));
    } catch (e) {
      if (e instanceof HttpErrorResponse && e.status === 404) {
        console.error(`${e.status}  ${e.error.message}`);
        return e.error as ReferenceIdSummary;
      }
    }
    return null;
  }

}

export interface ReferenceIdSummary {
  questionCount?: number,
  duration?: number,
  errorMessage?: string,
}
