import {Injectable} from "@angular/core";
import {
  ActivatedRouteSnapshot,
  CanActivate,
  CanActivateChild,
  Router,
  RouterStateSnapshot,
  UrlTree,
} from "@angular/router";
import {Observable} from "rxjs";
import {Roles} from "../../models/auth.model";
import {AUTH_GET_ID_TOKEN, IS_SESSION_VALID} from "../../models/auth.constants";

export interface RouteData {
  roles?: Roles[];
  common?: 'notLoggedIn' | 'loggedIn';
}

@Injectable()
export class AuthGuard implements CanActivate, CanActivateChild {

  constructor(private router: Router) {
  }

  private static async loggedInUserRole(): Promise<Roles> {
    const role = (await AUTH_GET_ID_TOKEN())["cognito:groups"][0]?.toUpperCase();
    return Roles[role];
  }

  private static async isEmailNotVerified(): Promise<boolean> {
    const idToken = await AUTH_GET_ID_TOKEN();
    return !idToken.email_verified;
  }

  canActivateChild(childRoute: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return this.validate(childRoute.parent.data, state.url);
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return this.validate(route.data, state.url);
  }

  private async validate(data: RouteData, url: string): Promise<boolean> {

    const isSessionValid: boolean = await IS_SESSION_VALID();

    if ((data.common === 'notLoggedIn' && !isSessionValid) ||
      (data.common === 'loggedIn' && isSessionValid)) {
      return true;
    }

    if (!isSessionValid) {
      return this.redirectToLogin(url);
    }

    if (await AuthGuard.isEmailNotVerified()) {
      return this.router.navigate(['/verify-email'], {
        queryParams: {'redirectURL': url}
      });
    }

    const loggedInUserRole: Roles = await AuthGuard.loggedInUserRole();
    if (data?.roles?.includes(loggedInUserRole)) {
      return true;
    } else {
      return this.router.navigate(["/home"]);
    }
  }

  private redirectToLogin(url: string) {
    return this.router.navigate(["/login"], {
      queryParams: {"redirectURL": url},
    });
  }
}
