import {Component, OnDestroy, OnInit} from '@angular/core';
import {FooterUpdateEvent, FooterUpdateService} from "../../services/footer/footer-update.service";
import {Subscription} from "rxjs";

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit, OnDestroy {
  showFooter: boolean = true;
  private subscription: Subscription;

  constructor(private footerUpdateService: FooterUpdateService) {
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngOnInit(): void {
    this.handleEvent();
  }

  private handleEvent() {
    this.subscription = this.footerUpdateService.getUpdate().subscribe(message => {
      switch (message) {
        case FooterUpdateEvent.START_TEST:
          this.showFooter = false;
          break;
        case FooterUpdateEvent.END_TEST:
          this.showFooter = true;
          break;
      }
    })
  }

}
