import {SelectionModel} from '@angular/cdk/collections';
import {HttpClient} from '@angular/common/http';
import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {MatDialog, MatDialogConfig} from '@angular/material/dialog';
import {MatTableDataSource} from '@angular/material/table';
import {SpinnerVisibilityService} from 'ng-http-loader';
import {ApiService} from 'src/app/api.service';
import {AlertDialogComponent} from 'src/app/util/alert-dialog/alert-dialog.component';
import {FormBuilder, FormControl, Validators} from "@angular/forms";

export interface UserData {
  position: number;
  name: string;
  skillid: number;
  hide: boolean;
  count: FormControl;
  complexity: string;
  custom_bank: boolean;
  live_code: boolean;
}

const ELEMENT_DATA: UserData[] = [
  // { position: 0, name: 'Angular', hide: false,count: 0 }
];

@Component({
  selector: 'app-create-reference',
  templateUrl: './create-reference.component.html',
  styleUrls: ['./create-reference.component.css']
})
export class CreateReferenceComponent implements OnInit {


  question_bank = []
  low_complexity = []
  medium_complexity = []
  high_complexity = []
  activateFlag: boolean = false
  selected_aptitude: boolean = false
  selected_psychometric: boolean = false
  testImagesFlag: boolean = false
  testVideoFlag: boolean = false
  interviewVideoFlag: boolean = false
  @ViewChild('fileInput', {static: true}) fileInput: ElementRef<HTMLInputElement>
  @ViewChild('fileInputquestions', {static: true}) fileInputquestions: ElementRef<HTMLInputElement>
  active_subscriptions: any
  selected_subscription: any = null
  total_questions: number = 0
  no_of_tests: boolean = false
  datasource_array_map = new Map<string, number>([
    ["key2", 0]
  ]);
  no_of_interviews: boolean = false
  duration: { hours: number, minutes: number } = {
    hours: 0,
    minutes: 30
  }
  job_id: any = null
  reference_name: any = null
  skills_array = []
  dataSource = new MatTableDataSource<UserData>(ELEMENT_DATA);
  displayedColumns: string[] = ['select', 'name'];
  selection = new SelectionModel<UserData>(true, []);
  selected_skills: UserData[] = [];
  private file: File;
  private file_questions: File;

  constructor(private matDialog: MatDialog,
              private http: HttpClient,
              private apiService: ApiService,
              private spinnerVisibilityService: SpinnerVisibilityService,
              private formBuilder: FormBuilder) {
  }

  get fileName(): string {
    return this.file?.name;
  }

  get fileName_questions(): string {
    return this.file_questions?.name;
  }

  get isInValid(): boolean {
    return this.dataSource.data
      .filter(data => data.hide)
      .map(data => data.count.invalid)
      .reduce((initial, current) => initial || current, false);
  }

  ngOnInit(): void {


    this.fetch_skills("")
    this.dataSource.data.forEach((element, index) => {
      this.low_complexity[index] = false
      this.high_complexity[index] = true
      this.medium_complexity[index] = true
      this.question_bank[index] = 'G'
    });
    this.fetch_subscriptions()
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  showSkills(row) {
    this.dataSource.data[row.position].hide = !this.dataSource.data[row.position].hide;
    this.complexity_change(true, 'M', row.position)
    this.selected_skills = this.dataSource.data
    if (this.dataSource.data[row.position].hide) {
      // this.skills_array.push({name:this.dataSource.data[row.position].name,position:this.dataSource.data[row.position].position,count:0})
      // this.datasource_array_map.set(row.position,this.skills_array.length-1)
      this.dataSource.data[row.position].count = this.getQuestionCountController(1)
    } else {
      this.dataSource.data[row.position].count = this.getQuestionCountController(0)
    }
    this.total_questions = 0
    this.dataSource.data.forEach(element => {
      if (element.hide) {
        this.total_questions = this.total_questions + Number(element.count.value)
      }
    });

    if (row.name == "Aptitude") {
      this.selected_aptitude = !this.selected_aptitude
    }
    if (row.name == "Psychometric") {
      this.selected_psychometric = !this.selected_psychometric
    }

  }

  fetch_skills(filterskill: any) {
    this.apiService.fetchSkills(filterskill).then((data) => {
      let skillarray: UserData[] = []
      data.forEach((element, index) => {
        skillarray.push({
          position: index,
          skillid: element.id,
          name: element.name,
          hide: false,
          count: this.getQuestionCountController(),
          complexity: "M",
          custom_bank: false,
          live_code: false
        })
      });
      let temp_dataSource = new MatTableDataSource<UserData>(skillarray);
      this.dataSource = temp_dataSource
    });
  }

  removeskill(skill) {
    this.dataSource.data[skill.position].hide = false;
    this.dataSource.data[skill.position].count = this.getQuestionCountController();
    this.selected_skills = this.dataSource.data
    this.total_questions = 0
    this.dataSource.data.forEach(element => {
      if (element.hide) {
        this.total_questions = this.total_questions + Number(element.count)
      }
    });
    if (skill.name == "Aptitude") {
      this.selected_aptitude = false
    }
    if (skill.name == "Psychometric") {
      this.selected_psychometric = false
    }

    // let temp_array = []
    // this.skills_array.forEach((element,index) => {
    //   index != i ? temp_array.push(element) : this.total_questions = this.total_questions - element.count
    // });
    // this.skills_array = temp_array
  }

  fetch_subscriptions() {
    this.apiService.fetchSubscriptions("").then((data) => {
      console.log(data)
      this.active_subscriptions = data
    })
  }

  updateTotal() {
    this.total_questions = this.dataSource.data
      .filter(data => data.hide)
      .map(data => data.count.value as number)
      .reduce((total, element) => total + element, 0);
  }

  create_reference() {
    console.log(this.fileName == null ? "its null" : this.fileName)
    console.log(this.fileName_questions == null ? "its null" : this.fileName_questions)
    const skills_array = this.dataSource.data
      .filter(data => data.hide)
      .map(data => {
        return {...data, count: data.count.value}
      });
    let input = {
      skills: skills_array,
      max_tests: this.no_of_tests,
      max_interviews: this.no_of_interviews,
      test_duration: this.duration,
      job_id: this.job_id,
      reference_name: this.reference_name,
      subscription: this.selected_subscription,
      activateFlag: this.activateFlag,
      testImagesFlag: this.testImagesFlag,
      testVideoFlag: this.testVideoFlag,
      interviewVideoFlag: this.interviewVideoFlag
    }

    if (this.verify_create_reference(input).input_valid) {
      console.log(input)

      this.spinnerVisibilityService.show();
      this.apiService.create_reference(input).then((data) => {
        console.log(data)
        if (data['return_message'] == "Reference Successfully Created") {
          if (this.fileName != null) {
            this.upload_file(data['reference_id'], this.file, "assets/corporate/reference/JD/")
          }
          if (this.fileName_questions != null) {
            this.upload_file(data['reference_id'], this.file_questions, "assets/corporate/reference/questions/")
          }
          this.openDialog(data['return_message'] + " & Reference ID is => " + data['reference_id'], "SUCCESS")
        } else {
          this.openDialog(data['return_message'], "ERROR")
        }
      }).finally(() => {
        this.spinnerVisibilityService.hide();
      });
    } else {
      this.openDialog(this.verify_create_reference(input).error_message, "ERROR")
    }

  }

  async fileChange(files: FileList) {
    if (files?.length > 0) {
      this.file = files[0];
    } else {
      this.file = undefined;
      this.fileInput.nativeElement.value = '';
    }
  }

  async fileChange_questions(files: FileList) {
    if (files?.length > 0) {
      this.file_questions = files[0];
    } else {
      this.file_questions = undefined;
      this.fileInputquestions.nativeElement.value = '';
    }
  }

  openDialog(message: string, type: string) {
    const dialogConfig = new MatDialogConfig();
    // dialogConfig.data = message;
    dialogConfig.data = {"message": message, "dialogLevel": type};
    let dialogRef = this.matDialog.open(AlertDialogComponent, dialogConfig);
  }

  upload_file(reference_id: any, file: any, path: any) {

    let data = {
      object_key: reference_id + ".pdf",
      // path: "assets/corporate/reference/JD/",
      path: path,
      method: "put_object"
    }

    this.apiService.generatePresignedUrl(data).then((data) => {
      console.log(data)
      this.http.put(data, file, {headers: {'Content-Type': file.type}}).subscribe((data) => {
        console.log(data)
        // this.openDialog("Video uploaded successfully", "success")
      })
    })
    // let questions_data = {
    //   object_key: "referenceid"+".pdf",
    //   path: "assets/corporate/reference/questions/",
    //   method: "put_object"
    // }
    // this.spinnerVisibilityService.show();
    // this.apiService.fetch_s3url(s3data).then((data) => {
    //   console.log(data)
    //   this.http.put(data, this.file, { headers: { 'Content-Type': this.file.type } }).subscribe((data) => {
    //     console.log(data)
    //     this.openDialog("Video uploaded successfully", "success")
    //   }).add(() => {
    //     this.spinnerVisibilityService.hide();
    //   });
    // }).finally(() => {
    //   this.spinnerVisibilityService.hide();
    // });
  }

  select_aptitude() {
    this.dataSource.data.forEach((element, index) => {
      if (element.name == "Aptitude") {
        if (this.selected_aptitude) {
          this.showSkills(this.dataSource.data[index])
        } else {
          this.removeskill(this.dataSource.data[index])
        }
      }
    });
  }

  select_psychometric() {
    this.dataSource.data.forEach((element, index) => {
      if (element.name == "Psychometric") {
        if (this.selected_psychometric) {
          this.showSkills(this.dataSource.data[index])
        } else {
          this.removeskill(this.dataSource.data[index])
        }
      }
    });
  }

  complexity_change(complexity, type, i) {
    if (complexity == true) {
      if (type == "L") {
        this.medium_complexity[i] = false
        this.high_complexity[i] = false
      }
      if (type == "M") {
        this.low_complexity[i] = false
        this.medium_complexity[i] = true;
        this.high_complexity[i] = false
      }
      if (type == "H") {
        this.low_complexity[i] = false
        this.medium_complexity[i] = false
      }
    }
    for (let x = 0; x <= Math.max(this.low_complexity.length, this.medium_complexity.length, this.high_complexity.length); x++) {
      this.dataSource.data[x].complexity = ""
      this.low_complexity[x] ? this.dataSource.data[x].complexity = "L" : ""
      this.medium_complexity[x] ? this.dataSource.data[x].complexity = "M" : ""
      this.high_complexity[x] ? this.dataSource.data[x].complexity = "H" : ""
    }
  }

  enable_test() {
    if (this.no_of_tests == false) {
      this.testImagesFlag = false
      this.testVideoFlag = false
    }
  }

  enable_interview() {
    if (this.no_of_interviews == false) {
      this.interviewVideoFlag = false
    }
  }

  verify_create_reference(input: any) {
    let error_message = ""
    let input_valid = true
    if (this.total_questions <= 0) {
      input_valid = false
      error_message = "Total questions selected should be > 0"
    }
    input.skills.forEach(element => {
      if (element.count == 0) {
        input_valid = false
        error_message = "Selected skill with 0 count"
      }
    });
    if (input.max_tests == 0 && input.max_interviews == 0) {
      input_valid = false
      error_message = "Select atleast one of tests or interviews"
    }
    if (input.subscription == null) {
      input_valid = false
      error_message = "Select Subscription"
    }
    if (input.job_id == null) {
      input_valid = false
      error_message = "Enter Job Id"
    }
    if (input.reference_name == null) {
      input_valid = false
      error_message = "Enter Reference Name"
    }
    if (this.fileName == null && input.max_interviews > 0) {
      input_valid = false
      error_message = "Upload JD for interview"
    }
    return {input_valid: input_valid, error_message: error_message}

  }

  initialize() {
    this.spinnerVisibilityService.show();
    this.question_bank = []
    this.low_complexity = []
    this.medium_complexity = []
    this.high_complexity = []
    this.activateFlag = false
    this.selected_aptitude = false
    this.selected_psychometric = false
    this.testImagesFlag = false
    this.testVideoFlag = false
    this.interviewVideoFlag = false
    this.file = null
    this.file_questions = null

    this.active_subscriptions = null
    this.selected_subscription = null
    this.total_questions = 0
    this.no_of_tests = false

    this.no_of_interviews = false
    this.duration = {
      hours: 0,
      minutes: 30
    }
    this.job_id = null
    this.reference_name = null

    this.skills_array = []
    this.selected_skills = [];
    this.ngOnInit()
    this.spinnerVisibilityService.hide();
  }

  private getQuestionCountController(value: number = 0) {
    return this.formBuilder.control(value, {
      validators: [
        Validators.min(1),
        Validators.max(20),
        Validators.required,
      ]
    });
  }

}
