import {Options} from '@angular-slider/ngx-slider';
import {SelectionModel} from '@angular/cdk/collections';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Component, Input, OnInit, ViewChild} from '@angular/core';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {DataTableDirective} from 'angular-datatables';
import {SpinnerVisibilityService} from 'ng-http-loader';
import {Subject} from 'rxjs';
import {ApiService} from 'src/app/api.service';
import {AUTH_GET_JWT_TOKEN} from "../../../../models/auth.constants";
import {AssetService} from "../../../../services/asset.service";
import {END_POINT} from "../../../../models/constants";

class reference_data {
  reference_id: string;
  job_id: string;
  status: string;
  date_created: string;
  tests_conducted: number;
  interviews_conducted: number;
  reference_description: string;
  subscription_id: number;
  subscription_description: string;
  max_tests: number;
  max_interviews: number;
}

class DataTablesResponse {
  data: any[];
  draw: number;
  recordsFiltered: number;
  recordsTotal: number;
}

class skills {
  name: string;
  count: number;
}
export interface populateDataTable {
  filters: {
    reference_id: string[],
    status: {
      active: number,
      inactive: number
    },
    date_range: {
      low: number,
      high: number
    },
    no_of_tests: {
      low: number,
      high: number
    },
    no_of_interviews: {
      low: number,
      high: number
    },
  }
  dataTableParameters: any
}

export interface referenceData {
  position;
  name: string;
  // description: string;
  hide: boolean;
}

const REFERENCE_DATA: referenceData[] = [
];

@Component({
  selector: 'app-view-reference',
  templateUrl: './view-reference.component.html',
  styleUrls: ['./view-reference.component.css']
})
export class ViewReferenceComponent implements OnInit {

  @Input() refresh_data:string;
  time: any

  clicked_reference_id = null
  tests_conducted = 0
  interviews_conducted = 0
  duration_hours = 0
  duration_minutes = 0
  subscription_description = ""
  job_id = ""
  reference_name = ""
  no_of_tests = 0
  no_of_interviews = 0
  testVideoFlag = false
  interviewVideoFlag = false
  testImagesFlag = false
  activateFlag = false
  jd_file = ""
  questions_file = ""
  total_questions = 0

  reference_skills: skills[] = [
    {
      name: "Core Java",
      count: 20
    },
    {
      name: "Core Java",
      count: 20
    },
  ]

  showfullreference = false
  test_options: Options = {
    floor: 0,
    ceil: 3000
  };
  interview_options: Options = {
    floor: 0,
    ceil: 1000
  };
  duration_options: Options = {
    floor: 0,
    ceil: 365
  };
  selected_reference: referenceData[] = [
  ];
  @ViewChild('referenceMatSort') referenceMatSort: MatSort;
  // displayedColumns_reference: string[] = ['select', 'name', 'description'];
  displayedColumns_reference: string[] = ['select', 'name'];
  dataSource_reference = new MatTableDataSource<referenceData>(REFERENCE_DATA);
  selection_reference = new SelectionModel<referenceData>(true, []);

  no_of_tests_low_value = 0
  no_of_tests_high_value = 3000
  no_of_interviews_low_value = 0
  no_of_interviews_high_value = 1000
  duration_low_value = 0
  duration_high_value = 90

  status = "All"

  @ViewChild(DataTableDirective, { static: false }) datatableElement: DataTableDirective;
  dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {};
  dtInstance: DataTables.Api;
  dtTrigger: Subject<any> = new Subject<any>();

  populateDataTable: populateDataTable = {
    filters: {
      reference_id: null,
      status: {
        active: null,
        inactive: null
      },
      date_range: {
        low: null,
        high: null
      },
      no_of_tests: {
        low: null,
        high: null
      },
      no_of_interviews: {
        low: null,
        high: null
      },
    },
    dataTableParameters: null
  }

  reference_data: reference_data[];

  constructor(private apiService: ApiService,
              private spinnerVisibilityService: SpinnerVisibilityService,
              private http: HttpClient,
              private asserService: AssetService) {
  }

  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }

  ngAfterViewInit(): void {
    this.dtTrigger.next(this.dtElement);
    this.refreshDataTable()
  }

  ngOnChanges(){
    this.fetch_references()
    this.refreshDataTable()
  }
  ngOnInit(): void {

    this.reference_skills = [
      {
        name: "Core Java",
        count: 20
      },
      {
        name: "Core Java",
        count: 20
      }
    ]

    this.fetch_references()
    this.refreshDataTable()
    const that = this;
    this.dtOptions = {
      // scrollCollapse: true,
      // "scrollY": "350px",
      scrollX: false,
      "order": [[ 3, "desc" ]],
      "autoWidth": false,
      pagingType: 'numbers',
      "language": {
        "search": "Search Job Id:"
      },

      searchDelay: 2000,
      lengthMenu: [5, 10, 25, 50, 100],
      columnDefs: [{
        'targets': [11],
        'orderable': false,

    }],
      responsive: true,
      dom: '<"top clearfix"lf><"table-responsive max-height-450 clearfix mb-1"rt><"clearfix"ip>',
      serverSide: true,
      processing: true,
      ajax: async (dataTablesParameters: any, callback) => {
        this.populateDataTable.dataTableParameters = dataTablesParameters
        let header = new HttpHeaders().set(
          "Authorization",
          localStorage.getItem("token")
        );
        that.http
          .post<DataTablesResponse>(
            `${END_POINT}/viewreference/`,
            this.populateDataTable, {
              headers: {
                Authorization: `Bearer ${await AUTH_GET_JWT_TOKEN()}`,
              }
            }
          ).subscribe(resp => {
            that.reference_data = resp.data['table'];
            console.log(resp);
            callback({
              recordsTotal: resp.recordsTotal,
              recordsFiltered: resp.recordsFiltered,
              data: []
            });
            this.windowresize()
          });
      },
      columns: [
        { data: 'reference_id' },
        { data: 'job_id' },
        { data: 'status' },
        { data: 'date_created' },
        { data: 'max_tests' },
        { data: 'max_interviews' },
        { data: 'tests_conducted' },
        { data: 'interviews_conducted' },
        { data: 'reference_description' },
        { data: 'subscription_id' },
        { data: 'subscription_description' },
        { data: 'view_reference' }
      ]
    };
    this.refreshDataTable()
  }
  windowresize() {
    setTimeout(function () {
      window.dispatchEvent(new Event('resize'))
    }, 100)
  }

  refreshDataTable() {
    let reference_id_array = []
    this.selected_reference.forEach(element => {
      element.hide ? reference_id_array.push(element.name) : ""
    });
    console.log(reference_id_array)
    this.populateDataTable = {
      filters: {
        no_of_tests: {
          low: this.no_of_tests_low_value,
          high: this.no_of_tests_high_value
        },
        no_of_interviews: {
          low: this.no_of_interviews_low_value,
          high: this.no_of_interviews_high_value
        },
        status: {
          active: (this.status == "Active" || this.status == "All") ? 1 : 2,
          inactive: (this.status == "Inactiveddd" || this.status == "All") ? 0 : 2
        },
        reference_id: reference_id_array,
        date_range: {
          low: this.duration_low_value,
          high: this.duration_high_value
        }
      },
      dataTableParameters: null
    }
    console.log(this.populateDataTable)
    setTimeout(() => {
      this.datatableElement.dtInstance.then((dtInstance: DataTables.Api) => {
        dtInstance.draw();
        dtInstance.columns.adjust().draw();
      });
    });
    this.windowresize()
  }

  applyFilter_reference(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource_reference.filter = filterValue.trim().toLowerCase();
  }

  showData_reference(event, i, row) {
    console.log(this.selection_reference.selected)

    this.dataSource_reference.data[row.position].hide = !this.dataSource_reference.data[row.position].hide;
    this.dataSource_reference.data[i].hide ? this.selection_reference.select(row) : ""
    this.selected_reference = this.dataSource_reference.data
    this.refreshDataTable()
  }

  fetch_references() {

    this.apiService.fetchReferrals().then((data) => {
      console.log(data)
      let referralsarray = []
      data.forEach((element, index) => {
        referralsarray.push({position: index, name: element, hide: false})
      });
      let temp_dataSource = new MatTableDataSource<referenceData>(referralsarray);
      this.dataSource_reference = temp_dataSource
    });
    // let temp_array = [
    //   {
    //     position: 0,
    //     name: "1d6264e9",
    //     description: "description for jd xxxx",
    //     hide: false
    //   }
    // ]
    // let temp_dataSource = new MatTableDataSource<referenceData>(temp_array);
    // this.dataSource_reference = temp_dataSource
  }

  showFullScreenReference(i) {
    this.clicked_reference_id = this.reference_data[i].reference_id
    this.getJdFile(this.reference_data[i].reference_id)
      .catch(err => console.error(err));
    this.getQuestionsFile(this.reference_data[i].reference_id)
      .catch(err => console.error(err));
    let input = {
      reference_id: this.reference_data[i].reference_id
    }
    this.spinnerVisibilityService.show();
    this.apiService.fetch_reference_details(input).then((data) => {
      console.log(data)
      let skills = JSON.parse(data[0].skills_and_count).skill_details
      console.log(skills)
      this.reference_skills = skills
      this.total_questions = 0
      data[0].status == "Active" ? this.activateFlag = true : this.activateFlag = false
      this.no_of_tests = data[0].max_tests
      this.job_id = data[0].job_id
      this.duration_hours = Math.floor(data[0].test_duration / 60)
      this.duration_minutes = data[0].test_duration % 60
      this.subscription_description = data[0].subscription_description
      this.reference_name = data[0].reference_description
      this.tests_conducted = data[0].tests_conducted
      this.interviews_conducted = data[0].interviews_conducted
      data[0].test_cam_on == 1 ? this.testImagesFlag = true : this.testImagesFlag = false
      data[0].test_video_on == 1 ? this.testVideoFlag = true : this.testVideoFlag = false
      this.no_of_interviews = data[0].max_interviews
      data[0].interview_video_on == 1 ? this.interviewVideoFlag = true : this.interviewVideoFlag = false
      console.log(this.activateFlag)
      skills.forEach(element => {
        this.total_questions = this.total_questions + parseInt(element.count)
      });
    }).finally(() => {
      this.spinnerVisibilityService.hide();
    });
    console.log(this.reference_skills[0].name)
    console.log(this.reference_data[i])
    this.showfullreference = true
  }

  get_timestamp() {
    this.time = (new Date()).getTime()
  }

  closeFullScreenReference() {
    this.showfullreference = false;
  }

  private async getJdFile(referenceId: string): Promise<void> {
    const uri = `assets/corporate/reference/JD/${referenceId}.pdf`;
    const isAvailable = await this.asserService.isAssetAvailable(uri);
    this.jd_file = isAvailable ? uri : null;
  }

  private async getQuestionsFile(referenceId: string): Promise<void> {
    const uri = "assets/corporate/reference/questions/" + referenceId + ".pdf";
    const isAvailable = await this.asserService.isAssetAvailable(uri);
    this.questions_file = isAvailable ? uri : null;
  }
}
