import {NgModule} from "@angular/core";
import {RouterModule, Routes} from "@angular/router";
import {RecruiterComponent} from "./recruiter/recruiter.component";
import {ProfilesComponent} from "./recruiter/profiles/profiles.component";
import {HomePageComponent} from "./home-page/home-page.component";
import {VideoLinksComponent} from "./recruiter/video-links/video-links.component";
import {ProfileComponent} from "./candidate/profile/profile.component";
import {HiretestComponent} from "./candidate/recruitment/hiretest/hiretest.component";
import {CandidateRegisterComponent} from "./candidate/candidate-register/candidate-register.component";
import {CandidateLoginComponent} from "./candidate/candidate-login/candidate-login.component";
import {RecruitmentComponent} from "./candidate/recruitment/recruitment.component";
import {ResumeComponent} from "./candidate/profile/resume/resume.component";
import {CandidateComponent} from "./candidate/candidate.component";
import {AssessmentComponent} from "./candidate/assessment/assessment.component";
import {NotFoundComponent} from "./error/not-found/not-found.component";
import {VerifytestsComponent} from "./admin/verifytests/verifytests.component";
import {CandidateSignInComponent} from "./auth/candidate-sign-in/candidate-sign-in.component";
import {CandidateSignUpComponent} from "./auth/candidate-sign-up/candidate-sign-up.component";
import {InterviewerComponent} from "./interviewer/interviewer.component";
import {AssessmentComponent as interviewassess} from "./interviewer/assessment/assessment.component";
import {TestComponent} from "../modules/test/test/test.component";
import {AboutComponent} from "./general/about/about.component";
import {ContactUsComponent} from "./general/contact-us/contact-us.component";
import {VerifytestVideosComponent} from "./admin/verifytest-videos/verifytest-videos.component";
import {PrivacyPolicyComponent} from "./general/privacy-policy/privacy-policy.component";
import {TermsConditionsComponent} from "./general/terms-conditions/terms-conditions.component";
import {AuthGuard, RouteData} from "../guards/auth/auth.guard";
import {Roles} from "../models/auth.model";
import {ViewQuestionsComponent} from "./questions/view-questions/view-questions.component";
import {UploadQuestionsComponent} from "./questions/upload-questions/upload-questions.component";
import {MyAccountComponent} from "./recruiter/my-account/my-account.component";
import {ChangePasswordComponent} from "./auth/change-password/change-password.component";
import {ResetPasswordComponent} from "./auth/reset-password/reset-password.component";
import {VerifyEmailComponent} from "./auth/verify-email/verify-email.component";

const routes: Routes = [
  {path: "", component: HomePageComponent},
  {path: "home", component: HomePageComponent},
  {
    path: "login", component: CandidateSignInComponent,
    canActivate: [AuthGuard],
    data: {common: 'notLoggedIn'} as RouteData,
  },
  {
    path: "sign-up", component: CandidateSignUpComponent,
    canActivate: [AuthGuard],
    data: {common: 'notLoggedIn'} as RouteData,
  },
  {
    path: "change-password",
    component: ChangePasswordComponent,
    canActivate: [AuthGuard],
    data: {common: 'loggedIn'} as RouteData,
  },
  {
    path: "reset-password",
    component: ResetPasswordComponent,
    canActivate: [AuthGuard],
    data: {common: 'notLoggedIn'} as RouteData,
  },
  {
    path: "verify-email",
    component: VerifyEmailComponent,
    canActivate: [AuthGuard],
    data: {common: 'loggedIn'} as RouteData,
  },
  {path: "register", component: CandidateRegisterComponent},
  {
    path: "candidate",
    component: CandidateComponent,
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {roles: [Roles.CANDIDATE]},
    children: [
      {path: "profile", component: ProfileComponent,},
      {path: "assessment", component: AssessmentComponent},
      {
        path: "recruitment", component: RecruitmentComponent,
        children: [
          {path: "hiretest", component: HiretestComponent},
        ],
      },
    ],
  },
  {
    path: "interviewer",
    component: InterviewerComponent,
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {roles: [Roles.INTERVIEWER]},
    children: [
      {path: "profile", component: ProfileComponent,},
      {path: "assessment", component: interviewassess},
    ],
  },
  {
    path: "recruiter",
    component: RecruiterComponent,
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {roles: [Roles.RECRUITER]},
    children: [
      {path: "profiles", component: ProfilesComponent,}, {path: "account", component: MyAccountComponent},
      {path: "video-links/:id", component: VideoLinksComponent,},
    ]
  },
  {
    path: 'admin',
    children: [
      {path: "verifytests/:user_id/:test_id", component: VerifytestsComponent,},
      {path: "verifytestvideos/:user_id/:test_id", component: VerifytestVideosComponent,},
      {path: "viewquestions", component: ViewQuestionsComponent,},
      {path: "uploadquestions", component: UploadQuestionsComponent,},
    ]
  },
  {
    path: "test/:data",
    canActivate: [AuthGuard],
    data: {roles: [Roles.CANDIDATE, Roles.INTERVIEWER]},
    component: TestComponent
  },
  {path: "about", component: AboutComponent,},
  {path: "contact-us", component: ContactUsComponent,},
  {path: "privacy-policy", component: PrivacyPolicyComponent,},
  {path: "terms-and-conditions", component: TermsConditionsComponent,},
  {path: '**', component: NotFoundComponent,},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {
}

export const RoutingComponent = [
  CandidateLoginComponent,
  CandidateRegisterComponent,
  CandidateComponent,
  ProfileComponent,
  ResumeComponent,
  HomePageComponent,
];

// remember to put all parent modules into above RoutingComponent
