import {HttpClient, HttpHeaders, HttpXhrBackend} from "@angular/common/http";
import {END_POINT, HTTP_HEADERS} from "./constants";
import {firstValueFrom} from "rxjs";


abstract class Recorder {
  protected recorder: MediaRecorder;
  protected readonly http = new HttpClient(new HttpXhrBackend({
    build: () => new XMLHttpRequest()
  }));

  protected chunks: Blob[] = []

  protected constructor(protected readonly stream: MediaStream, protected readonly testId: string) {
    this.recorder = new MediaRecorder(stream);
    this.recorder.onstop = () => this.onStop();
    this.recorder.ondataavailable = (blobEvent: BlobEvent) => this.onDataAvailable(blobEvent);
  }

  startRecord(timeInSec: number = 5) {
    this.recorder.start(timeInSec * 1000);
  }

  stopRecording(): void {
    if (this.recorder.state === 'recording') {
      this.recorder.stop();
    }
    this.stream.getTracks().forEach(track => track.stop())
  }

  protected onStop(): void {
    this.flush()
      .catch(err => console.error(err))
  }

  protected async flush(): Promise<void> {
    const URL = `${END_POINT}/${this.getURI()}`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    const putURL = await firstValueFrom(this.http.put<string>(URL, {
      testId: this.testId,
      id: new Date().getTime()
    }, {headers: httpHeaders}));
    await firstValueFrom(this.http.put(putURL, new Blob(this.chunks, {type: 'video/webm'})))
  }

  protected abstract getMaxSize(): number;

  protected abstract getURI(): string;

  protected onDataAvailable(blobEvent: BlobEvent): void {
    this.chunks.push(blobEvent.data)
    if (this.chunks.map(chunk => chunk.size).reduce((prev, curr) => prev + curr, 0) > this.getMaxSize()) {
      this.flush()
        .then(_ => this.chunks = [])
        .catch(err => console.error(err));
    }
  }

}

export class WebCamRecorder extends Recorder {
  private static readonly MAX_SIZE: number = 1.1 * 1000 * 1000


  constructor(override stream: MediaStream, override testId: string) {
    super(stream, testId);
  }

  protected override getMaxSize(): number {
    return WebCamRecorder.MAX_SIZE;
  }

  protected override getURI(): string {
    return "v1/test/chunk";
  }

}

export class ScreenRecorder extends Recorder {
  private static readonly MAX_SIZE: number = 1.1 * 1000 * 1000

  constructor(override stream: MediaStream, override testId: string, private candidateId: number) {
    super(stream, testId);
  }

  protected getMaxSize(): number {
    return ScreenRecorder.MAX_SIZE;
  }

  protected getURI(): string {
    return `v1/interview/chunk/${this.candidateId}`;
  }

}

export class AudioTrackNotAvailableException extends Error {
}

export class ImageCapture {
  constructor(private stream: MediaStream,
              private canvas: HTMLCanvasElement,
              private video: HTMLVideoElement,
  ) {
    this.video.srcObject = stream;
    this.video.play().catch(err => console.error(err));
  }

  get imageAsBase64(): string {
    this.drawImageToCanvas(this.video);

    return this.canvas.toDataURL("image/png").split(',')[1];
  }

  get imageAsDataUrl(): string {
    this.drawImageToCanvas(this.video);
    return this.canvas.toDataURL("image/png");
  }

  closeStream() {
    this.stream.getTracks().forEach(track => track.stop())
  }

  private drawImageToCanvas(image: any) {
    this.canvas
      .getContext("2d")
      .drawImage(image, 0, 0);
  }

}
