import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { FormBuilder } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import {ApiService} from "../../api.service";

@Component({
  selector: 'app-candidate-register',
  templateUrl: './candidate-register.component.html',
  styleUrls: ['./candidate-register.component.css']
})
export class CandidateRegisterComponent implements OnInit {

  constructor(private formBuilder: FormBuilder,private apiService:ApiService,private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    this.apiService.getNews().subscribe((data)=>{
      this.test = JSON.stringify(data)
      console.log(this.test);
    });
 }

  checkoutForm = this.formBuilder.group({
    name: '',
    email: '',
    password: '',
    mobno: '8800544771',
    exp_years: 10,
    exp_months: 5,
    resume: new FormControl('', [Validators.required]),
    fileSource: new FormControl('', [Validators.required]),
    terms_conditions: new FormControl('', [Validators.required])
  });

  private file: File;

  title = 'training-app';
  test: String;

 onFileChange(event) {

  if (event.target.files.length > 0) {
    const file = event.target.files[0];
    this.checkoutForm.patchValue({
      fileSource: file
    });
  }
}


 onClickSubmit(): void {
  this.router.navigate(['login']);
   console.log(this.checkoutForm.get('terms_conditions').value);
  // Process checkout data here
  const formData = new FormData();
  formData.append('file', this.checkoutForm.get('fileSource').value);

  this.apiService.postFile(formData).subscribe((data)=>{
    console.log(data);
  });


  console.warn('Your order has been submitted', this.checkoutForm.value.resume);
  this.apiService.getNews().subscribe((data)=>{
    this.test = JSON.stringify(data)
    this.checkoutForm.patchValue({name: this.test,email: ''});
  });

  this.apiService.postNews(JSON.stringify(this.checkoutForm.value)).subscribe((data)=>{
    console.log(data);
  });

  console.log(JSON.stringify(this.checkoutForm.value))
}
}
