import {Component, ElementRef, OnDestroy, OnInit, Renderer2, ViewChild} from '@angular/core';
import {Auth} from "aws-amplify";
import {Router} from "@angular/router";
import {HeaderUpdateEvent, HeaderUpdateService} from "../../services/header/header-update.service";
import {Subscription} from "rxjs";
import {ProfileImageService} from "../../services/candidate/my-profile/profile-image.service";
import {HeaderRoute, HeaderService} from "../../services/header/header.service";
import {GET_FULL_NAME, IS_SESSION_VALID} from "../../models/auth.constants";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit, OnDestroy {

  @ViewChild('profileImage', {static: false}) profileImage: ElementRef<HTMLImageElement>;

  showHeader: boolean = true;
  full_name: string;
  isSessionActive: boolean;
  isValidRoute: boolean = true;
  navs: Array<HeaderRoute>;
  private subscription: Subscription;

  constructor(private router: Router,
              private headerUpdateService: HeaderUpdateService,
              private profileImageService: ProfileImageService,
              private renderer: Renderer2,
              private headerService: HeaderService) {
    this.handleUpdateEvents();
  }

  private _active: number;

  get active(): number {
    return this._active;
  }

  set active(i: number) {
    this._active = i;
  }

  async ngOnInit(): Promise<void> {
    await this.init();
  }

  signOut() {
    Auth.signOut()
      .then(() => this.headerUpdateService.sendUpdate(HeaderUpdateEvent.SIGN_OUT))
      .catch(err => console.error(err))
      .finally(() => this.router.navigate(['/home']))
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  isActive(uri: string): boolean {
    return this.router.url === uri;
  }

  private async init() {
    try {
      this.isSessionActive = await IS_SESSION_VALID();
      this.navs = await this.headerService.getHeaders();
      this.delayFullNameAndProfileImageDisplay();
    } catch (err) {
      console.log(err);
    }
  }

  private delayFullNameAndProfileImageDisplay() {
    setTimeout(async () => {
      if (this.isSessionActive && this.showHeader) {
        this.full_name = await GET_FULL_NAME();
        this.setProfileImage();
      }
    }, 250);
  }

  private handleUpdateEvents() {
    this.subscription = this.headerUpdateService.getUpdate().subscribe(message => {
      switch (message) {
        case HeaderUpdateEvent.FOUND:
          this.isValidRoute = true;
          this.ngOnInit();
          break;
        case HeaderUpdateEvent.NOT_FOUND:
          this.isValidRoute = false;
          break;
        case HeaderUpdateEvent.SIGN_IN:
        case HeaderUpdateEvent.SIGN_OUT:
          this.ngOnInit();
          break;
        case HeaderUpdateEvent.IMAGE_CHANGE:
          this.setProfileImage();
          break;
        case HeaderUpdateEvent.START_TEST:
          this.showHeader = false;
          break
        case HeaderUpdateEvent.END_TEST:
          this.showHeader = true;
          break;
      }
    });
  }

  private setProfileImage() {
    this.profileImageService.getProfileImage()
      .then(src => this.renderer.setAttribute(this.profileImage.nativeElement, 'src', src))
      .catch(err => console.error(err));
  }
}
