import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { SpinnerVisibilityService } from "ng-http-loader";
import { ApiService } from 'src/app/api.service';
import { AlertDialogComponent } from 'src/app/util/alert-dialog/alert-dialog.component';

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent implements OnInit {

  name: string;
  email: string;
  subject: string;
  message: string;

  constructor(private matDialog: MatDialog, private http: HttpClient, private spinnerVisibilityService: SpinnerVisibilityService,private apiService: ApiService) { }

  ngOnInit(): void {
    this.name = ""
    this.email = ""
    this.subject = ""
    this.message = ""

  }

  openDialog(message: string, type: string) {
    const dialogConfig = new MatDialogConfig();
    // dialogConfig.data = message;
    dialogConfig.data = { "message": message, "dialogLevel": type };
    let dialogRef = this.matDialog.open(AlertDialogComponent, dialogConfig);
  }

  sendmessage(){
    let error_message = ""
    if (this.name == ""){
      error_message = "Please enter your name"
    }
    if (this.email == ""){
      error_message = "Please enter your email"
    }
    if (this.subject == ""){
      error_message = "Please enter Subject"
    }
         
  if (error_message == ""){
    let message = {
      name: this.name,
      email: this.email,
      subject: this.subject,
      message: this.message
    }
    console.log("atul")
    this.spinnerVisibilityService.show();
    this.apiService.contactus(message).then((data) => {
      console.log(data)
    }).finally(() => {
      this.spinnerVisibilityService.hide();
      this.openDialog("We have receive your enquiry and our representative will get back to you shortly. Thanks!", "SUCCESS")
    });    
  }else{
    this.openDialog(error_message, "ERROR")
  }
  }
}
