import {Injectable} from '@angular/core';
import {IdToken, Roles} from "../../models/auth.model";
import {AUTH_GET_ID_TOKEN, IS_SESSION_VALID} from "../../models/auth.constants";

@Injectable({
  providedIn: 'root'
})
export class HeaderService {

  private static getCandidateHeader(): Array<HeaderRoute> {
    return [
      {name: 'Home', route: '/candidate'},
      {name: 'About', route: '/about'},
      {name: 'My Profile', route: '/candidate/profile'},
      {name: 'Assessment', route: '/candidate/assessment'},
      {name: 'Contact Us', route: '/contact-us'},
    ];
  }

  private static getAdminHeader(): Array<HeaderRoute> {
    return [
      {name: 'Home', route: '/candidate'},
      {name: 'About', route: '/candidate'},
      {name: 'My Profile', route: '/candidate'},
      {name: 'Assessment', route: '/candidate'},
      {name: 'Contact Us', route: '/contact-us'},
    ];
  }

  private static getRecruiterHeader(): Array<HeaderRoute> {
    return [
      {name: 'Home', route: '/recruiter'},
      {name: 'About', route: '/about'},
      {name: 'Profiles', route: '/recruiter/profiles'},
      {name: 'Account', route: '/recruiter/account'},
      {name: 'Contact Us', route: '/contact-us'},
    ];
  }

  private static getInterviewerHeader(): Array<HeaderRoute> {
    return [
      {name: 'Home', route: '/interviewer'},
      {name: 'About', route: '/about'},
      {name: 'My Profile', route: '/interviewer/profile'},
      {name: 'Assessment', route: '/interviewer/assessment'},
      {name: 'Contact Us', route: '/contact-us'},
    ];
  }

  private static getGuestHeader(): Array<HeaderRoute> {
    return [
      {name: 'Home', route: '/'},
      {name: 'About', route: '/about'},
      {name: 'Candidate', route: '/candidate'},
      {name: 'Corporate', route: '/recruiter'},
      {name: 'Interviewer', route: '/interviewer'},
      {name: 'Contact Us', route: '/contact-us'},
    ];
  }


  private static async isSessionValid(): Promise<boolean> {
    return IS_SESSION_VALID();
  }

  private static async getRole(): Promise<Roles> {
    const token: IdToken = await AUTH_GET_ID_TOKEN();
    return Roles[token["cognito:groups"][0].toUpperCase()]
  }

  public async getHeaders(): Promise<Array<HeaderRoute>> {

    if (!await HeaderService.isSessionValid()) {
      return HeaderService.getGuestHeader();
    }

    switch (await HeaderService.getRole()) {
      case Roles.ADMIN:
        return HeaderService.getAdminHeader();
      case Roles.CANDIDATE:
        return HeaderService.getCandidateHeader();
      case Roles.RECRUITER:
        return HeaderService.getRecruiterHeader();
      case Roles.INTERVIEWER:
        return HeaderService.getInterviewerHeader();
    }
  }
}

export interface HeaderRoute {
  name: string;
  route: string;
}
