import {Component, ElementRef, OnInit, Renderer2, ViewChild} from '@angular/core';
import {ResumeService} from "../../../../services/candidate/my-profile/resume.service";
import {AlertDialogComponent} from "../../../util/alert-dialog/alert-dialog.component";
import {MatDialog} from "@angular/material/dialog";

@Component({
  selector: 'candidate-resume',
  templateUrl: './resume.component.html',
  styleUrls: ['./resume.component.css']
})
export class ResumeComponent implements OnInit {

  @ViewChild('resume', {static: true}) resume: ElementRef<HTMLObjectElement>;

  @ViewChild('fileInput', {static: true}) fileInput: ElementRef<HTMLInputElement>
  private file: File;

  constructor(private resumeService: ResumeService,
              private renderer: Renderer2,
              private matDialog: MatDialog,) {
  }

  get isValidFile(): boolean {
    return this.file === undefined || this.file?.name?.toUpperCase().endsWith('.PDF');
  }

  get isFileSelected(): boolean {
    return this.file !== undefined;
  }

  get fileName(): string {
    return this.file?.name;
  }

  async ngOnInit(): Promise<void> {
    await this.getResume();
  }

  async getResume(): Promise<void> {
    try {
      const src: string = await this.resumeService.getResume();
      if (src.indexOf("default.pdf") === -1) {
        this.renderer.removeAttribute(this.resume.nativeElement, 'data');
        this.renderer.setAttribute(this.resume.nativeElement, 'data', src);
      }
    } catch (err) {
      console.error(err);
    }
  }

  async fileChange(files: FileList) {
    if (files?.length > 0) {
      this.file = files[0];
    } else {
      this.file = undefined;
      this.fileInput.nativeElement.value = '';
    }
  }

  click(event) {
    console.log(event)
  }

  async uploadResume() {
    await this.resumeService.uploadResume(this.file)
    this.matDialog.open(AlertDialogComponent, {data: {message: 'resume updated'}});
    await this.getResume();
  }


}
