  ngOnInit(): void {
    this.fetch_skills("")
    this.fetch_locations()
    this.fetch_referrals()
    this.fetch_job_ids()
    this.recruiterService.get_recruiters_profiles(null).then(data => console.log(data)); // TODO remove this console log
    this.video_play()
    this.video_play_test()
    this.timing_event_test = setInterval(() => {
      this.video_play_test();
    }, 5000);

    this.timing_event = setInterval(() => {
      this.video_play();
    }, 5000);

    const that = this;
    this.dtOptions = {
      // destroy: true,
      scrollCollapse: true,
      // "scrollY": "350px",
      // scrollX: true,

      "autoWidth": true,
      pagingType: 'numbers',
      "language": {
        "search": "Search Name:"
      },
      searchDelay: 2000,
      lengthMenu: [5, 10, 25, 50, 100],
      responsive: true,
      dom: '<"top clearfix"lf><"table-responsive max-height-450 clearfix mb-1"rt><"clearfix"ip>',
      serverSide: true,
      processing: true,
      ajax: async (dataTablesParameters: any, callback) => {
        this.populateDataTable.dataTableParameters = dataTablesParameters
        let header = new HttpHeaders().set(
          "Authorization",
          localStorage.getItem("token")
        );
        that.http
          .post<DataTablesResponse>(
            `${END_POINT}/recruiterprofiles/`,
            // dataTablesParameters, {}
            this.populateDataTable, {
              headers: {
                Authorization: `Bearer ${await AUTH_GET_JWT_TOKEN()}`,
              }
            }
          ).subscribe(resp => {
            that.profiles_data = resp.data['table'];
            that.datatable_row_keys = resp.data['row_keys'];
            console.log(that.skill_wise_scores)
            if (that.populateDataTable.filters.reference.length == 1) {
              that.skill_wise_scores = resp['skill_wise_scores']
            }
            else{
              that.skill_wise_scores = {}
            }
            console.log(that.skill_wise_scores)
            console.log(resp);
            // console.log(that.skill_wise_scores['0'][0]['name'])
            callback({
              recordsTotal: resp.recordsTotal,
              recordsFiltered: resp.recordsFiltered,
              // infoFiltered: "",
              // recordsTotal: 10000,
              // recordsFiltered: 10000,
              // draw: 0,
              data: []
            });
            this.windowresize()
          });
      },
      // columns: [{ data: 'referral_id' },{ data: 'description' }, { data: 'name' }, { data: 'technology' }, { data: 'salary' }, { data: 'mobile' }, { data: 'location' },{ data: 'test_score' }, { data: 'interview_score' }, { data: 'test_id' }, { data: 'interview_id' }, { data: 'interview_schedule' }, { data: 'interview_status' }]
      // columns: this.populateDataTable.filters.reference.length == 1 ? this.reference_columns : this.columns_default
      columns: this.reference_columns
    };
    this.refreshDataTable();
  }

  ngOnDestroy(): void {
    if (this.timing_event) {
      clearInterval(this.timing_event);
    }
    if (this.timing_event_test) {
      clearInterval(this.timing_event_test);
    }
    this.dtTrigger.unsubscribe();
  }


