  refreshDataTable() {
    let skills_array = []
    let locations_array = []
    let referrals_array = []
    let job_id_array = []

    this.selected_skills.forEach(element => {
      element.hide ? skills_array.push(element.name) : ""
    });

    this.dataSource_location.data.forEach(element => {
      element.hide ? locations_array.push(element.name) : ""
    });

    this.dataSource_referrals.data.forEach(element => {
      element.hide ? referrals_array.push(element.name) : ""
    });

    this.dataSource_job_ids.data.forEach(element => {
      element.hide ? job_id_array.push(element.name) : ""
    });

    this.populateDataTable = {
      filters: {
        salary: {
          low: this.salary_low_value,
          high: this.salary_high_Value
        },
        experience: {
          low: this.exp_low_value,
          high: this.exp_high_Value
        },
        skills: skills_array,
        locations: locations_array,
        reference: referrals_array,
        job_id: job_id_array,
        test_score: {
          low: this.test_low_value,
          high: this.test_high_Value
        }
      },
      dataTableParameters: null
    }
    setTimeout(() => {
      // this.dtInstance.destroy()
      this.datatableElement.dtInstance.then((dtInstance: DataTables.Api) => {
        // if (this.isDtInitialized) {
        //   this.dtInstance.destroy()
        //   this.dtTrigger.next(this.dtElement);
        // } else {
        //   this.isDtInitialized = true
        //   // this.dtTrigger.next(this.dtElement);
        // }
        dtInstance.draw();
        dtInstance.columns.adjust().draw();
        for (let i = 0; i < 10; i++) {
          dtInstance.columns(13 + i + 1).visible(false)
        }
        for (let i = 0; i < this.show_columns; i++) {
          dtInstance.columns(13 + i + 1).visible(true)
        }
      });
    });
    this.windowresize()
  }

