  fetch_skills(filterskill: any) {
    this.apiService.fetchSkills(filterskill).then((data) => {
      let skillarray: UserData[] = []
      data.forEach((element, index) => {
        skillarray.push({ position: index, skillid: element.id, name: element.name, hide: false })
      });
      let temp_dataSource = new MatTableDataSource<UserData>(skillarray);
      this.dataSource = temp_dataSource
    });
  }

  fetch_locations() {
    this.apiService.fetchLocations().then((data) => {
      let locationarray: locationData[] = []
      data.forEach((element, index) => {
        locationarray.push({ position: index, name: element, hide: false })
      });
      let temp_dataSource = new MatTableDataSource<locationData>(locationarray);
      this.dataSource_location = temp_dataSource
    });
  }


