import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {TestService} from "../../../services/test/test.service";

declare const navigator: any;

@Component({
  selector: 'app-start-test',
  templateUrl: './start-test.component.html',
  styleUrls: ['./start-test.component.css']
})
export class StartTestComponent implements OnInit {
  @Output() startTestChange = new EventEmitter<void>();
  @Output() cancelTestChange = new EventEmitter<void>();
  @Input() referenceId: string;
  audioAndCameraPermissions: PermissionState = null;

  constructor(private testService: TestService) {
    //
  }

  private _errorMessage: string = null;

  get errorMessage() {
    return this._errorMessage;
  }

  async ngOnInit(): Promise<void> {
    await this.checkCameraAndAudioPermissions();
  }

  _cancelTest() {
    this.cancelTestChange.emit();
  }

  async checkCameraAndAudioPermissions(): Promise<void> {
    try {
      const stream: MediaStream = await navigator.mediaDevices.getUserMedia({video: true, audio: true})
      if (stream.active) {
        this.audioAndCameraPermissions = 'granted'
        stream.getTracks().forEach(track => track.stop());
      }
      this._errorMessage = null;
    } catch (e) {
      this.audioAndCameraPermissions = 'denied';
      if (e.message === "Could not start video source") {
        this._errorMessage = "Could not start video source, camera might already in use or no camera source available";
      } else {
        this._errorMessage = "You have denied request to use camera please reset settings";
      }
    }
  }

  async _startTest() {
    const ref = await this.testService.validateReferenceId(this.referenceId);
    await this.checkCameraAndAudioPermissions();
    if (ref?.errorMessage) {
      this._errorMessage = ref.errorMessage;
    } else {
      this._errorMessage = null;
      if (this.audioAndCameraPermissions === 'granted') {
        this.startTestChange.emit();
      }
    }

  }

}
