import {Component, OnDestroy, OnInit} from '@angular/core';
import {Location} from "@angular/common";
import {HeaderUpdateEvent, HeaderUpdateService} from "../../../services/header/header-update.service";

@Component({
  selector: 'app-not-found',
  templateUrl: './not-found.component.html',
  styleUrls: ['./not-found.component.css']
})
export class NotFoundComponent implements OnInit, OnDestroy {

  constructor(private headerUpdateService: HeaderUpdateService,
              private location: Location,) {
  }

  ngOnInit(): void {
    this.headerUpdateService.sendUpdate(HeaderUpdateEvent.NOT_FOUND)
  }

  ngOnDestroy(): void {
    this.headerUpdateService.sendUpdate(HeaderUpdateEvent.FOUND)
  }

  gobBack() {
    this.location.back();
  }

}
