import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { SpinnerVisibilityService } from 'ng-http-loader';
import { ApiService } from 'src/app/api.service';
import * as XLSX from 'xlsx';

export interface initiated_references_input {
  reference_id: string
}

@Component({
  selector: 'app-initiated-references',
  templateUrl: './initiated-references.component.html',
  styleUrls: ['./initiated-references.component.css']
})
export class InitiatedReferencesComponent implements OnInit {
  full_timeline: boolean
  @Input() reference_id: string;
  @Input() timestamp: any;   // this is done to referesh component upon every click

  initiated_references_input: initiated_references_input = null
  reference_data: any = []

  private file: File;
  constructor(private spinnerVisibilityService: SpinnerVisibilityService, private apiService: ApiService,) { }

  ngOnInit(): void {
    this.initiated_references()
  }

  ngOnChanges() {
    console.log(this.reference_id)
    this.initiated_references()
    console.log((new Date()).getTime())
  }


  initiated_references() {
    this.initiated_references_input = {
      reference_id: this.reference_id,
    }
    this.reference_data = []
    this.spinnerVisibilityService.show();
    this.apiService.initiatedReferences(this.initiated_references_input).then((data) => {
      let temp_array = []
      console.log(data)
      if (this.full_timeline) {
        this.reference_data = data
      } else {
        data.forEach(element => {
          let element_found = false
          temp_array.forEach((temp,index) => {
            if (element.name == temp.name && element.email == temp.email && element.phone == temp.phone){
              element_found = true
              if (element.time > temp.time){
                temp_array[index].time = element.time
                temp_array[index].status = element.status
                temp_array[index].next_steps = element.next_steps
              }
            }
          });
          if (!element_found){
            temp_array.push(element)
          }
        });
        this.reference_data = temp_array
      }
    }).finally(() => {
      this.spinnerVisibilityService.hide();
    });
  }

  exportexcel(): void {
    /* table id is passed over here */
    let element = document.getElementById('excel-table-reference');
    const ws: XLSX.WorkSheet = XLSX.utils.table_to_sheet(element);

    /* generate workbook and add the worksheet */
    const wb: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

    /* save to file */
    XLSX.writeFile(wb, "testfile.xlsx");

  }
  show_full_timeline() {
    console.log(this.full_timeline)
    this.initiated_references()
  }
}
