import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {Profile, SalariesEnum} from "../../../models/candidate.model";
import {END_POINT, HTTP_HEADERS} from "../../../models/constants";
import {firstValueFrom} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class PersonalDetailsService {

  constructor(private http: HttpClient) {
  }

  public async fetchPersonalDetails(): Promise<Profile> {
    const URL = `${END_POINT}/v1/candidate/personal-details`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    return firstValueFrom(this.http.get<any>(URL, {headers: httpHeaders}))
      .then(resp => {
        return {
          fullName: resp.fullName,
          experience: resp.experience,
          emailAddress: resp.emailAddress,
          location: resp.location,
          mobileNumber: resp.mobileNumber,
          salary: SalariesEnum[resp.salary] === undefined ? '' : SalariesEnum[resp.salary],
          openForChange: resp.openForChange === 'Y',
        }
      });
  }

  public async updatePersonalDetails(profile: Profile) {
    const URL = `${END_POINT}/v1/candidate/personal-details`;
    const httpHeaders: HttpHeaders = await HTTP_HEADERS();
    return firstValueFrom(this.http.put(URL, profile, {headers: httpHeaders}));
  }

}
