import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormControl, Validators} from "@angular/forms";
import {Auth} from "aws-amplify";
import {MatDialog} from "@angular/material/dialog";
import {AlertDialogComponent, AlertDialogData} from "../../util/alert-dialog/alert-dialog.component";
import {ActivatedRoute, Router} from "@angular/router";
import {AUTH_GET_ID_TOKEN} from "../../../models/auth.constants";

@Component({
  selector: 'app-verify-email',
  templateUrl: './verify-email.component.html',
  styleUrls: ['./verify-email.component.css']
})
export class VerifyEmailComponent implements OnInit {

  otpFormControl: FormControl = this.formBuilder.control('', {validators: Validators.required})

  constructor(private formBuilder: FormBuilder,
              private matDialog: MatDialog,
              private route: ActivatedRoute,
              private router: Router) {
    //
  }

  private _errorMessage: string;

  get errorMessage(): string {
    return this._errorMessage;
  }

  private static async refreshSession() {
    try {
      const cognitoUser = await Auth.currentAuthenticatedUser();
      const currentSession = await Auth.currentSession();
      cognitoUser.refreshSession(currentSession.getRefreshToken());
    } catch (e) {
      console.log('Unable to refresh Token', e);
    }
  }

  async getOtp() {
    try {
      await Auth.verifyCurrentUserAttribute('email');
      this.matDialog.open(AlertDialogComponent, {data: {message: 'OTP is sent to your registered email address'}});
    } catch (e) {
      this._errorMessage = e.message;
    }
  }

  async verifyEmail() {
    try {
      await Auth.verifyCurrentUserAttributeSubmit('email', this.otpFormControl.value);
      await VerifyEmailComponent.refreshSession();
      this.matDialog.open<AlertDialogComponent, AlertDialogData>(AlertDialogComponent, {
        data: {
          message: 'Email Verified',
          onClose: this.postVerifyEmail(),
        }
      });
    } catch (e) {
      this._errorMessage = e.message;
    }
  }

  async ngOnInit(): Promise<void> {
    const idToken = await AUTH_GET_ID_TOKEN();
    if (idToken.email_verified) {
      await this.router.navigate(['/home']);
    } else {
      await this.getOtp();
    }
  }

  private postVerifyEmail() {
    return async () => {
      const uri = this.route.snapshot.queryParamMap.get('redirectURL');
      if (uri !== null) {
        await this.router.navigate([this.route.snapshot.queryParamMap.get('redirectURL')]);
      }
    }

  }

}
