import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {END_POINT, HTTP_HEADERS} from "../../models/constants";
import {firstValueFrom} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class ReferenceAgenciesService {

  constructor(private http: HttpClient) {
  }

  public async getReferenceAgencies(): Promise<{ id: number, name: string }[]> {
    const URL = `${END_POINT}/v1/reference-agencies`;
    return firstValueFrom(this.http.get<any>(URL));
  }

}
