import { SelectionModel } from '@angular/cdk/collections';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { SpinnerVisibilityService } from 'ng-http-loader';
import { ApiService } from 'src/app/api.service';
import { AlertDialogComponent } from 'src/app/util/alert-dialog/alert-dialog.component';

export interface UserData {
  position: number;
  name: string;
  skillid: number;
  hide: boolean;
  count: number;
}
const ELEMENT_DATA: UserData[] = [
  // { position: 0, name: 'Angular', hide: false,count: 0 }
];

@Component({
  selector: 'app-upload-questions',
  templateUrl: './upload-questions.component.html',
  styleUrls: ['./upload-questions.component.css']
})
export class UploadQuestionsComponent implements OnInit {
  mapping_skills = []
  dataSource = new MatTableDataSource<UserData>(ELEMENT_DATA);
  displayedColumns: string[] = ['select', 'name'];
  selection = new SelectionModel<UserData>(true, []);
  selected_skills: UserData[] = [
  ];
  question:any
  constructor(private apiService: ApiService,private spinnerVisibilityService: SpinnerVisibilityService,private matDialog: MatDialog) { }

  ngOnInit(): void {
    this.fetch_skills("")
  }

  uploadquestion(){
    if (this.IsJsonString(this.question)) {
    let input = {
      questions: this.question,
      skills: this.mapping_skills
    }
    this.spinnerVisibilityService.show();
    this.apiService.uploadquestions(input).then((data) => {
      console.log(data)
      this.openDialog("Updated successfully","success")
    }).finally(() => {
      this.spinnerVisibilityService.hide();
    });
  }
  else{
    this.openDialog("Enter valid json","success")
  }
  }

  openDialog(message: string,type: string) {
    const dialogConfig = new MatDialogConfig();
    // dialogConfig.data = message;
    dialogConfig.data = { "message": message, "type": type};
    let dialogRef = this.matDialog.open(AlertDialogComponent, dialogConfig);
  }

  IsJsonString(str) {
    try {
        JSON.parse(str);
    } catch (e) {
        return false;
    }
    return true;
}
applyFilter(event: Event) {
  const filterValue = (event.target as HTMLInputElement).value;
  this.dataSource.filter = filterValue.trim().toLowerCase();
}

showSkills(row) {
  this.mapping_skills = []
  this.dataSource.data[row.position].hide = !this.dataSource.data[row.position].hide;
  this.selected_skills = this.dataSource.data
  this.selected_skills.forEach(element => {
    element.hide ? this.mapping_skills.push(element.skillid) : ""
  });
}

fetch_skills(filterskill: any) {
  this.apiService.fetchSkills(filterskill).then((data) => {
    let skillarray: UserData[] = []
    data.forEach((element, index) => {
      skillarray.push({ position: index, skillid: element.id, name: element.name, hide: false, count: 0 })
    });
    let temp_dataSource = new MatTableDataSource<UserData>(skillarray);
    this.dataSource = temp_dataSource
  });
}
}
