import {Component, OnInit} from '@angular/core';
import {
  AbstractControl,
  AsyncValidatorFn,
  FormBuilder,
  FormControl,
  FormGroup,
  ValidationErrors,
  Validators
} from "@angular/forms";
import {Auth} from "aws-amplify";
import {Observable} from "rxjs";
import {AuthService} from "../../../services/auth/auth.service";
import {SpinnerVisibilityService} from "ng-http-loader";
import {CountryISO, PhoneNumberFormat, SearchCountryField} from "ngx-intl-tel-input";
import {PasswordValidators} from "../../../util/validators.util";

@Component({
  selector: 'app-candidate-sign-up',
  templateUrl: './candidate-sign-up.component.html',
  styleUrls: ['./candidate-sign-up.component.css']
})
export class CandidateSignUpComponent implements OnInit {

  signUpForm: FormGroup;
  signUpSuccess: boolean = false;
  error: Error = null;

  separateDialCode = false;
  readonly SearchCountryField = SearchCountryField;
  readonly CountryISO = CountryISO;
  readonly PhoneNumberFormat = PhoneNumberFormat;
  readonly preferredCountries: CountryISO[] = [CountryISO.UnitedStates, CountryISO.UnitedKingdom];

  constructor(private formBuilder: FormBuilder,
              private authService: AuthService,
              private spinnerVisibilityService: SpinnerVisibilityService,
  ) {
  }

  get userName(): FormControl {
    return this.signUpForm.get('userName') as FormControl;
  }

  get name(): FormControl {
    return this.signUpForm.get('name') as FormControl;
  }

  get email(): FormControl {
    return this.signUpForm.get('email') as FormControl;
  }

  get phoneNumber(): FormControl {
    return this.signUpForm.get('phoneNumber') as FormControl;
  }

  get password(): FormControl {
    return this.signUpForm.get('password') as FormControl;
  }

  get confirmPassword(): FormControl {
    return this.signUpForm.get('confirmPassword') as FormControl;
  }

  get isValidConfirmPassword(): boolean {
    return this.signUpForm.errors?.['confirmPassword'] === undefined;
  }

  get userType(): FormControl {
    return this.signUpForm.get('userType') as FormControl;
  }


  ngOnInit(): void {
    this.signUpForm = this.formBuilder.group({
      'userName': this.formBuilder.control('', {
        validators: [
          Validators.required
        ], asyncValidators: [
          this.checkUsernameAvailable
        ], updateOn: 'blur',
      }),
      'email': this.formBuilder.control('', {
        validators: [
          Validators.email,
          Validators.required
        ]
      }),
      'name': this.formBuilder.control('', {
        validators: [
          Validators.required
        ], updateOn: 'blur',
      }),
      'phoneNumber': this.formBuilder.control('', {
        validators: [
          Validators.required,
        ], updateOn: 'blur',
      }),
      'password': this.formBuilder.control('', {
        validators: [
          Validators.required,
          PasswordValidators.checkValidPassword,
        ], updateOn: 'blur',
      }),
      'confirmPassword': this.formBuilder.control('', {
        validators: [
          Validators.required,
        ], updateOn: 'blur',
      }),
      'userType': this.formBuilder.control('C', {
        validators: [
          Validators.required,
        ], updateOn: 'change',
      })
    }, {
      validators: [
        PasswordValidators.checkConfirmPassword,
      ],
    })
  }

  async signUp() {
    try {
      const signUpFormValue = this.signUpForm.value;
      this.spinnerVisibilityService.show();
      await Auth.signUp({
        username: signUpFormValue.userName,
        password: signUpFormValue.password,
        attributes: {
          email: signUpFormValue.email,
          name: signUpFormValue.name,
          phone_number: signUpFormValue.phoneNumber.e164Number,
          'custom:userType': signUpFormValue.userType,
        }
      });
      this.signUpSuccess = true;
    } catch (e) {
      this.error = e;
      if (this.error.message.indexOf('user_details_user_type_phone_UN') != -1) {
        e.message = 'Phone number already in use. Please try another.';
      } else if (this.error.message.indexOf('user_details_user_type_email_id_UN') != -1) {
        e.message = 'This email address is already taken. Please try another or consider signing in.'
      }
      this.signUpSuccess = false;
    }
    this.spinnerVisibilityService.hide();
  }

  private checkUsernameAvailable: AsyncValidatorFn = (control: AbstractControl): Promise<ValidationErrors | null> | Observable<ValidationErrors | null> => {
    const user_name = control.value;
    return (async () => {
      const userNameAvailable = await this.authService.userNameAvailable(user_name);
      return userNameAvailable ? null : {message: "User Name not available"};
    })();

  }

}
