import {Component, Input} from '@angular/core';
import {AudioTrackNotAvailableException, ScreenRecorder} from "../../../../models/webcam.model";

@Component({
  selector: 'app-interview-record-button',
  templateUrl: './interview-record-button.component.html',
  styleUrls: ['./interview-record-button.component.css']
})
export class InterviewRecordButtonComponent {

  private static readonly MAX_RECORD_TIME: number = 45 * 60 * 1000;
  @Input() name: string;
  @Input() interviewId: string;
  @Input() candidateId: string;
  screenRecorder: ScreenRecorder;

  recording: boolean = false;

  constructor() {
    //
  }

  private _errorMessage: string;

  get errorMessage(): string {
    return this._errorMessage;
  }


  private static async getSystemAudioAndScreenStream() {
    const systemAudioAndStream = await navigator.mediaDevices.getDisplayMedia(
      {
        audio: true,
        video: {
          width: 3000, height: 2000
        }
      })

    if (systemAudioAndStream.getAudioTracks().length == 0) {
      systemAudioAndStream.getTracks().forEach(track => track.stop());
      throw new AudioTrackNotAvailableException("no audio track available")
    }
    return systemAudioAndStream;
  }

  async startRecording() {
    try {
      this._errorMessage = null;
      const systemAudioAndStream = await InterviewRecordButtonComponent.getSystemAudioAndScreenStream();
      const micAudioStream = await navigator.mediaDevices.getUserMedia({audio: true})
      const combinedAudioStream = this.combineAudioTracks([...micAudioStream.getAudioTracks(), ...systemAudioAndStream.getAudioTracks()]);
      const mediaStream = new MediaStream([...systemAudioAndStream.getVideoTracks(), ...combinedAudioStream.getAudioTracks()])
      this.screenRecorder = new ScreenRecorder(mediaStream, this.interviewId, parseInt(this.candidateId));
      setTimeout(() => this.stopRecording(), InterviewRecordButtonComponent.MAX_RECORD_TIME);
      this.screenRecorder.startRecord(2);
      this.recording = true;
    } catch (err) {
      if (err instanceof AudioTrackNotAvailableException) {
        this._errorMessage = err.message;
      }
    }
  }

  stopRecording() {
    this.screenRecorder?.stopRecording();
    this.recording = false;
  }

  private combineAudioTracks(audioTracks: MediaStreamTrack[]): MediaStream {
    const audioIns: MediaStreamAudioSourceNode[] = [];
    const audioContext = new AudioContext();
    for (let audioTrack of audioTracks) {
      let mediaStream = new MediaStream();
      mediaStream.addTrack(audioTrack);
      audioIns.push(audioContext.createMediaStreamSource(mediaStream))
    }
    const dest = audioContext.createMediaStreamDestination();
    audioIns.forEach(audioIn => audioIn.connect(dest))
    return dest.stream;
  }

}


