import {Component, ElementRef, Input, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {ApiService} from "../../../api.service";
import {ImageCapture, WebCamRecorder} from "../../../../models/webcam.model";


@Component({
  selector: 'app-web-cam',
  templateUrl: './web-cam.component.html',
  styleUrls: ['./web-cam.component.css']
})
export class WebCamComponent implements OnInit, OnDestroy {

  readonly WIDTH = 640;
  readonly HEIGHT = 480;
  image_count = 1;
  @Input() testId: string;
  @Input() recordImage: boolean;
  @Input() recordVideo: boolean;
  @Input() maxImageCount: number = 10;
  @ViewChild("video") private video: ElementRef<HTMLVideoElement>;
  @ViewChild("canvas") private canvas: ElementRef<HTMLCanvasElement>;
  private timingEvent: NodeJS.Timer
  private webCamRecorder: WebCamRecorder;
  private imageCapture: ImageCapture;
  private readonly timeBeforeImage = (Math.random() * 5 * 1000) + 5 * 1000;

  constructor(private apiService: ApiService,) {
    //
  }

  ngOnDestroy(): void {
    if (this.recordVideo) {
      this.webCamRecorder.stopRecording();
    }

    if (this.recordImage) {
      this.imageCapture.closeStream();
    }
    clearInterval(this.timingEvent);
  }

  async ngOnInit() {
    if (this.recordVideo) {
      await this.videoRecorderInit();
    }

    if (this.recordImage) {
      await this.imageRecorderInit();
    }

    this.timingEvent = setInterval(async () => await this.record(), this.timeBeforeImage);
  }

  private async imageRecorderInit() {
    const stream = await navigator.mediaDevices.getUserMedia({
      video: true,
    });
    this.imageCapture = new ImageCapture(stream, this.canvas.nativeElement, this.video.nativeElement)
  }

  private async videoRecorderInit() {
    const stream = await navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true,
    });
    this.webCamRecorder = new WebCamRecorder(stream, this.testId);
    this.webCamRecorder.startRecord(1)
  }

  private async handleImage(webcamImage: string): Promise<void> {
    const imageData = {
      image: webcamImage,
      // id: (new Date()).getTime(),
      id: this.image_count % this.maxImageCount,
      testId: this.testId,
    };
    this.image_count = this.image_count + 1
    try {
      await this.apiService.sendimage(imageData);
    } catch (e) {
      console.error(e)
    }
  }

  private async record() {
    if (this.recordImage) {
      await this.handleImage(this.imageCapture.imageAsBase64);
    }
  }
}
